import React, {
    createContext,
    ReactNode,
    useCallback,
    useContext,
    useState
} from 'react';
import { IEnvironment } from '../types';

type SecurityFieldsProviderProps = {
    children: ReactNode;
};

type SecurityFieldData = {
    environment: IEnvironment | undefined;
    encryptKey: string | undefined;
};

export type SecurityFieldsState = {
    environment: IEnvironment | undefined;
    encryptKey: string | undefined;
    setInitState: (data: SecurityFieldData) => void;
};

const initConfigState = {} as SecurityFieldsState;

const SecurityFieldsContext = createContext<SecurityFieldsState>(
    initConfigState
);

const SecurityFieldsProvider = ({ children }: SecurityFieldsProviderProps) => {
    const [environment, setEnvironment] = useState<IEnvironment | undefined>();
    const [encryptKey, setEncryptKey] = useState<string | undefined>();

    const handleInitState = useCallback(
        (data: SecurityFieldData) => {
            const { environment: env, encryptKey: key } = data;

            setEnvironment(env);
            setEncryptKey(key);
        },
        [setEnvironment, setEncryptKey]
    );

    return (
        <SecurityFieldsContext.Provider
            value={{ setInitState: handleInitState, environment, encryptKey }}
        >
            {children}
        </SecurityFieldsContext.Provider>
    );
};

const useSecurityFields = (): SecurityFieldsState => {
    const context = useContext(SecurityFieldsContext);

    if (!context) {
        throw new Error(
            'useSecurityFields must be used within a SecurityFieldsProvider'
        );
    }

    return context;
};

export { SecurityFieldsProvider, useSecurityFields };
