import React, { useCallback, useEffect } from 'react';
import { ErrorBoundary } from './components/ErrorBoundary';
import {
    Payment as PaymentComponent,
    PaymentProps as PaymentComponentProps
} from './Payment';
import { PaymentComponentProvider } from './hooks';
import SentryHelperFactory from './utils/helpers/SentryHelperFactory';
import { IBaseError } from './entities';
import './styles.module.css';

export type PaymentError = {
    internalMessage: string;
    message: string;
    code: string | undefined;
};

export type PaymentProps = {
    onError?: (error: PaymentError) => void;
} & Omit<PaymentComponentProps, 'onError'>;

export const Payment: React.FC<PaymentProps> = ({
    onError,
    environment = 'development',
    ...restProps
}) => {
    useEffect(() => {
        SentryHelperFactory.builder(environment);
    }, [environment]);

    const onErrorOccur = useCallback(
        (error: IBaseError) => {
            SentryHelperFactory.builder(environment).handleError(
                error.exception
            );

            onError &&
                onError({
                    message: error.clientMessage,
                    internalMessage:
                        error.exception?.code || error.exception?.name || '',
                    code: error.code
                });
        },
        [onError, environment]
    );

    return (
        <div id='payment-root' className='container'>
            <ErrorBoundary onError={onErrorOccur}>
                <PaymentComponentProvider>
                    <PaymentComponent
                        environment={environment}
                        {...restProps}
                        onError={onErrorOccur}
                    />
                </PaymentComponentProvider>
            </ErrorBoundary>
        </div>
    );
};
