import * as yup from 'yup';

export const BrowserRequestDataSchema = yup.object().shape({
    remoteaddr: yup.string().required('Atributo remoteaddr é obrigatório'),
    host: yup.string().required('Atributo host é obrigatório'),
    serveraddr: yup.string().required('Atributo serveraddr é obrigatório'),
    servername: yup.string().required('Atributo servername é obrigatório'),
    port: yup.string().required('Atributo port é obrigatório'),
    scheme: yup.string().required('Atributo scheme é obrigatório'),
    useragent: yup.string().required('Atributo useragent é obrigatório'),
    browser: yup.string().required('Atributo browser é obrigatório')
});
