import { Formatter } from './Formatter';

export interface IDateFormatter extends Formatter<string, string> {
    split: (date: string) => string[];
}

export class DateFormatter implements IDateFormatter {
    // return MM/YYYY
    format(date: string): string {
        if (!date) {
            return '';
        }
        const [month, year] = String(date).split('/');
        const newDate = new Date(Number(year) + 2000, Number(month) - 1);

        return `${month}/${newDate.getFullYear()}`;
    }

    /**
     *
     * @param date string (mm/yyyy)
     * @returns string[] [yyyy, mm]
     */
    split(date: string): string[] {
        if (!date) {
            return [];
        }
        const [month, year] = String(date).split('/');
        const newDate = new Date(Number(year) + 2000, Number(month) - 1);

        return [month, String(newDate.getFullYear())];
    }
}
