import { BrowserDataValidationException } from '../exceptions';
import { FetchBrowserDataData } from '../services/requests/FetchBrowserDataRequestService';
import { BrowserRequestDataSchemaValidator } from '../utils/validations/validators';
import { BrowserData } from './BrowserData';

export class BrowserDataFactory {
    static async builder(data: FetchBrowserDataData): Promise<BrowserData> {
        const externalIpSchemaValidator = new BrowserRequestDataSchemaValidator();

        try {
            await externalIpSchemaValidator.validate(data);
        } catch (error) {
            throw new BrowserDataValidationException(
                `Dados da requisição de dados do navegador inválido(s): ${error.message}`
            );
        }

        const {
            browser,
            useragent,
            host,
            port,
            remoteaddr,
            scheme,
            serveraddr,
            servername
        } = data;

        return new BrowserData(
            remoteaddr,
            host,
            serveraddr,
            servername,
            port,
            scheme,
            useragent,
            browser
        );
    }
}
