import * as Sentry from '@sentry/react';
import { Integrations } from '@sentry/tracing';
import { sentry } from '../../configs/sentry';
import { environment } from '../../configs/environment';
import { ISentryHelper } from '../../interfaces/Sentry';

export class HomologSentryHelper implements ISentryHelper {
    readonly client: Sentry.BrowserClient;
    constructor() {
        this.client = new Sentry.BrowserClient({
            dsn: sentry.dsn,
            environment: environment.HOMOLOG,
            integrations: [new Integrations.BrowserTracing()],
            normalizeDepth: 10,
            maxValueLength: 1000
        });
    }

    handleError(error: any) {
        this.client.captureException(error);
    }
}
