import { FetchServiceOperatorException } from '../../exceptions';
import { Model } from './Model';
import { RequestService, RequestResponse } from './RequestService';

export type FetchServiceOperatorData = {
    data: any;
    statuscode: number;
    servicos: [
        {
            idservico: number;
            idoperadora: number;
            merchantaccount: [
                { merchantaccount: number; tipocredencial: number }
            ];
        }
    ];
};

type FetchServiceOperatorError = {
    messageerror: string;
    internalmessageerror: string;
};

export type FetchServiceOperatorSuccessResponseData = {
    data: any;
    statuscode: number;
    servico: Array<FetchServiceOperatorData>;
};

type FetchServiceOperatorErrorResponseData = {
    statuscode: number;
    error: Array<FetchServiceOperatorError>;
};

export type FetchServiceOperatorResponseData = FetchServiceOperatorSuccessResponseData &
    FetchServiceOperatorErrorResponseData;

export class FetchServiceOperatorRequestService extends Model<FetchServiceOperatorSuccessResponseData> {
    constructor(
        readonly requestService: RequestService<FetchServiceOperatorSuccessResponseData>,
        readonly body?: any,
        readonly tokenStorageService?: any
    ) {
        super(requestService);
    }

    fetch = async (): Promise<
        RequestResponse<FetchServiceOperatorSuccessResponseData>
    > => {
        try {
            return await this.post({
                body: JSON.stringify(this.body)
            });
        } catch (error) {
            throw new FetchServiceOperatorException(error);
        }
    };
}
