import {
    RequestService,
    RequestConfig,
    RequestPromise
} from './RequestService';

/**
 * Class representing the Model. It represents all possible base requests
 * @template T Response data
 */

export class Model<T> {
    /**
     * Create the Model
     * @param {RequestService<T>} requestService Request service
     */
    constructor(readonly requestService: RequestService<T>) {}

    /**
     * Method that make the get request
     * @param {RequestConfig} config Request config such as headers, url, data, paramns, ...
     * @returns {RequestPromise<T|undefined>} Returns a request promise with response data T
     */
    get(config?: RequestConfig): RequestPromise<T> {
        return this.requestService.request({
            method: 'get',
            ...config
        });
    }

    /**
     * Method that make the post request
     * @param {RequestConfig} config Request config such as headers, url, data, paramns, ...
     * @returns {RequestPromise<T>} Returns a request promise with response data T
     */
    post(config?: RequestConfig): RequestPromise<T> {
        return this.requestService.request({
            method: 'post',
            ...config
        });
    }

    /**
     * Method that make the put request
     * @param {RequestConfig} config Request config such as headers, url, data, paramns, ...
     * @returns {RequestPromise<T>} Returns a request promise with response data T
     */
    put(config?: RequestConfig): RequestPromise<T> {
        return this.requestService.request({
            method: 'put',
            ...config
        });
    }

    /**
     * Method that make the patch request
     * @param {RequestConfig} config Request config such as headers, url, data, paramns, ...
     * @returns {RequestPromise<T>} Returns a request promise with response data T
     */
    patch(config?: RequestConfig): RequestPromise<T> {
        return this.requestService.request({
            method: 'patch',
            ...config
        });
    }

    /**
     * Method that make the delete request
     * @param {RequestConfig} config Request config such as headers, url, data, paramns, ...
     * @returns {RequestPromise<T>} Returns a request promise with response data T
     */
    delete(config?: RequestConfig): RequestPromise<T> {
        return this.requestService.request({
            method: 'delete',
            ...config
        });
    }
}
