import React, { useState, useCallback, useEffect } from 'react';
import { CheckMarkIcon, FieldErrorIcon } from '../../icons';
import { SecurityInput, InputProps } from '../security-input';

type CardHolderProps = {
    onValueValidated?: () => void;
    onValueError?: () => void;
} & Omit<InputProps, 'id' | 'iframeParam'>;

export const CardHolder: React.FC<CardHolderProps> = ({
    icon,
    onChange,
    onValueValidated,
    onValueError,
    onBlur,
    errors,
    className,
    value: valueFromProps,
    ...restProps
}) => {
    const [cardHolderIcon, setCardHolderIcon] = useState<
        React.SVGProps<SVGSVGElement> | undefined
    >(icon || undefined);
    const [valid, setValid] = useState<boolean>(false);

    useEffect(() => {
        if (errors?.length) {
            setCardHolderIcon(<FieldErrorIcon />);
        }
    }, [errors]);

    const handleOnInputChange = useCallback(
        (data: PostMessageEventDataPagarMe) => {
            const { isValid } = data;

            setValid(isValid);
            if (isValid) {
                onValueValidated && onValueValidated();
                setCardHolderIcon(<CheckMarkIcon />);
            } else {
                setCardHolderIcon(undefined);
            }
            onChange && onChange(data);
        },
        [onChange, onValueValidated, setCardHolderIcon]
    );

    const handleOnBlur = useCallback(
        (data: PostMessageEventDataPagarMe) => {
            const { isValid, isFilled } = data;

            if (isValid) {
                setCardHolderIcon(<CheckMarkIcon />);
                onValueValidated && onValueValidated();
            } else if (isFilled) {
                onValueError && onValueError();
                setCardHolderIcon(<FieldErrorIcon />);
            } else {
                setCardHolderIcon(undefined);
            }
            onBlur && onBlur(data);
        },
        [onBlur, setCardHolderIcon, onValueValidated, onValueError]
    );

    return (
        <SecurityInput
            id='card-holder-pagarme'
            icon={icon || cardHolderIcon}
            errors={errors}
            valid={valid}
            containerClassName={className}
            {...restProps}
            value={valueFromProps}
            onChange={handleOnInputChange}
            onBlur={handleOnBlur}
        />
    );
};
