import * as yup from 'yup';

export const CreditCardPagarMeSchema = yup.object().shape({
    holder: yup.string().required('Atributo holder é obrigatório'),
    brand: yup.string().required('Atributo brand é obrigatório'),
    cardtoken: yup.string().required('Atributo cardtoken é obrigatório')
});
