import { useCallback, useEffect, useState } from 'react';
import {
    BaseErrorFactory,
    BrowserDataFactory,
    FingerPrint,
    FingerPrintFactory,
    IBaseError,
    Uuid,
    UuidFactory
} from '../entities';
import {
    FetchBrowserDataRequestServiceFactory,
    FetchFingerPrintRequestServiceFactory,
    FetchUuidRequestServiceFactory
} from '../services/requests';
import { IBrowser, IEnvironment } from '../types';
import {
    OrgIdHelper,
    PaymentDataBuilderHelperFactory,
    PaymentPagarMeDataBuilderHelperFactory,
    SessionIdHelper
} from '../utils/helpers';

type InitData = {
    environment: IEnvironment;
};

type SendData = {
    cardNumber: {
        adyenEncryptedValue: string;
        gazinEncryptedValue: string;
        pagarMeValue?: string;
        maskedValue?: string;
    };
    expiryDate: {
        adyenEncryptedValue: {
            expiryMonth: string;
            expiryYear: string;
        };
        pagarMeValue?: {
            expiryMonth: string;
            expiryYear: string;
        };
        gazinEncryptedValue: string;
    };
    securityCode: {
        adyenEncryptedValue: string;
        gazinEncryptedValue: string;
        pagarMeValue?: string;
    };
    nameOnCard: {
        adyenEncryptedValue: string;
        gazinEncryptedValue: string;
        pagarMeValue?: string;
    };
    installment: string;
    cardNumberPasted: boolean;
    brand: string;
    fingerPrint: FingerPrint | undefined;
    browserData: IBrowser | undefined;
    uuid: Uuid | undefined;
    adyenFingerPrint: string | undefined;
};

export const usePayment = ({ environment }: InitData) => {
    const [fingerPrint, setFingerPrint] = useState<FingerPrint>();
    const [uuid, setUuid] = useState<Uuid>();
    const [browserData, setBrowserData] = useState<IBrowser | undefined>(
        undefined
    );
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState<IBaseError>();
    const isLoaldingComponent = useCallback(async () => {
        setIsLoading(true);
    }, [setIsLoading]);

    useEffect(() => {
        let ignore = false;

        async function fetchInitData() {
            try {
                const [
                    fetchBrowserDataResponse,
                    fingerPrintResponse,
                    uuidResponse
                ] = await Promise.all([
                    FetchBrowserDataRequestServiceFactory.builder(
                        environment
                    ).fetch(),
                    FetchFingerPrintRequestServiceFactory.builder(
                        environment
                    ).fetch(),
                    FetchUuidRequestServiceFactory.builder(environment).fetch()
                ]);

                const browserData = await BrowserDataFactory.builder(
                    fetchBrowserDataResponse.data.data
                );
                const fingerPrintEntity = await FingerPrintFactory.builder(
                    fingerPrintResponse.data.data
                );
                const uuidEntity = await UuidFactory.builder(
                    uuidResponse.data.data
                );

                const browser = {
                    cookiesaccepted: window.navigator.cookieEnabled,
                    hostname: window.location.hostname,
                    ipaddress: browserData.remoteAddr,
                    type: browserData.browser,
                    useragent: browserData.userAgent,
                    screenheight: window.screen.height,
                    screenwidth: window.screen.width,
                    colordepth: window.screen.colorDepth,
                    javaenabled: window.navigator.javaEnabled(),
                    language: window.navigator.language,
                    timezoneoffset: new Date().getTimezoneOffset()
                };

                // Braspag script
                const ORG_ID = OrgIdHelper.get(environment);
                const SESSION_ID_NAME = SessionIdHelper.get(environment);
                let script = document.getElementById('h-online-metrix');

                if (!script) {
                    script = document.createElement('script');
                    script.id = 'h-online-metrix';
                }
                // @ts-ignore
                script.src = `https://h.online-metrix.net/fp/tags.js?org_id=${ORG_ID}&session_id=${SESSION_ID_NAME}${fingerPrintEntity.value}`;

                // @ts-ignore
                script.async = true;
                document.head.appendChild(script);

                if (!ignore) {
                    setFingerPrint(fingerPrintEntity);
                    setUuid(uuidEntity);
                    setBrowserData(browser);
                }
            } catch (error) {
                const err = BaseErrorFactory.build(
                    'Erro ao inicializar pagamento',
                    error
                );
                setError(err);
            }

            if (!ignore) {
                setIsLoading(false);
            }
        }

        fetchInitData();

        return () => {
            ignore = true;
        };
    }, []);

    const formatPaymentData = useCallback(async (values: SendData) => {
        const {
            cardNumberPasted,
            cardNumber,
            expiryDate,
            securityCode,
            nameOnCard,
            installment,
            brand,
            fingerPrint,
            browserData,
            uuid,
            adyenFingerPrint
        } = values;

        const paymentData = await PaymentDataBuilderHelperFactory.builder().build(
            {
                fingerPrint,
                browserData,
                uuid,
                adyenFingerPrint,
                cardNumber,
                expiryDate,
                securityCode,
                nameOnCard,
                installment,
                cardNumberPasted,
                brand
            }
        );

        return paymentData;
    }, []);

    const formatPaymentDataPagarme = useCallback(async (values: any) => {
        const {
            cardtoken,
            cardNumberPasted,
            cardNumber,
            expiryDate,
            securityCode,
            nameOnCard,
            installment,
            brand,
            fingerPrint,
            browserData,
            uuid,
            adyenFingerPrint
        } = values;

        const paymentData = await PaymentPagarMeDataBuilderHelperFactory.builder().build(
            {
                cardtoken,
                fingerPrint,
                browserData,
                uuid,
                adyenFingerPrint,
                cardNumber,
                expiryDate,
                securityCode,
                nameOnCard,
                installment,
                cardNumberPasted,
                brand
            }
        );

        return paymentData;
    }, []);

    return {
        fingerPrint,
        uuid,
        browserData,
        isLoading,
        error,
        formatPaymentData,
        formatPaymentDataPagarme,
        isLoaldingComponent
    };
};
