import { GetUrlBaseHelper } from './../../utils/helpers';
import { RequestService } from './RequestService';
import {
    FetchBrowserDataRequestService,
    FetchBrowserDataResponseData
} from './FetchBrowserDataRequestService';
import { IEnvironment } from '../../types';

export class FetchBrowserDataRequestServiceFactory {
    private static fetchBrowserDataRequestService: FetchBrowserDataRequestService;

    static builder(environment?: IEnvironment): FetchBrowserDataRequestService {
        if (
            !FetchBrowserDataRequestServiceFactory.fetchBrowserDataRequestService
        ) {
            const urlBase = GetUrlBaseHelper.get(environment);
            const requestService = new RequestService<FetchBrowserDataResponseData>(
                `${urlBase}/pagamentos/navegador`
            );
            FetchBrowserDataRequestServiceFactory.fetchBrowserDataRequestService = new FetchBrowserDataRequestService(
                requestService
            );
        }

        return FetchBrowserDataRequestServiceFactory.fetchBrowserDataRequestService;
    }
}
