import React from 'react';
import { CheckoutButton } from '../CheckoutButton';
import { Header } from './Header';
import { CreditCardForm, IAmount, SubmitValues } from './CreditCardForm';
import styles from './styles.module.css';
import { useConfig } from '../../hooks';
import { FieldError } from '../../types';

type CreditCardProps = {
    amount?: IAmount;
    onSubmit: (values: SubmitValues) => void;
    disabledButton:boolean | undefined
    fieldsError?: (erros?: FieldError) => void;
};

export const CreditCard: React.FC<CreditCardProps> = ({
    amount,
    onSubmit,
    fieldsError,
    disabledButton
}) => {
    const {
        paymentTypeTitle,
        checkoutButtonTitle,      
        noSelectIstallmentLabel
    } = useConfig();

    const onFormSubmit = (values: SubmitValues) => {
        const {
            cardNumberPasted,
            cardNumber,
            expiryDate,
            securityCode,
            nameOnCard,
            installment,
            brand
        } = values;
        onSubmit({
            cardNumberPasted,
            cardNumber,
            expiryDate,
            securityCode,
            nameOnCard,
            installment,
            brand
        });
    };

    const onFieldsError = (erros?: FieldError) => {
        if (fieldsError && erros) fieldsError(erros);
    };
    return (
        <div className={`${styles.paymentContainer} payment-container`}>
            <div className={`${styles.cardPaymentContainer} card-container`}>
                <Header title={paymentTypeTitle as string} />
                <div className='card-content'>
                    <CreditCardForm
                        onSubmit={onFormSubmit}
                        amount={amount}
                        fieldsError={onFieldsError}
                        noSelectIstallmentLabel={noSelectIstallmentLabel}
                    />
                </div>             
                    <div className={`${styles.cardFooter} card-footer`}>
                        <CheckoutButton
                            form='credit-card-form'
                            className='checkout-button'
                            title={checkoutButtonTitle}
                            disabled={disabledButton}
                        />
                    </div>
            </div>
        </div>
    );
};
