import styles from './styles.module.css';
import { FormFields } from '../../../types';
import { useCreditCardForm } from '../../../hooks';
import { Card } from '../../../configs/creditCards';
import React, { FormEvent, useCallback, useEffect } from 'react';
import { FetchPagarMeTokenRequestServiceFactory } from '../../../services/requests/FetchPagarMeTokenRequestService.Factory';
import { formFieldsPagarMeValidationHelper } from '../../../utils/helpers/FormFieldsValidationHelperFactoryPagarMe';
import {
    FieldError,
    Fields,
    FieldsNames,
    SecurityFields,
    SecurityFieldsNames
} from '../../../types/creditCardForm';
import {
    FormErrorHelper,
    formFieldsValidationHelper,
    GetFormFieldErrorsByFieldName
} from '../../../utils/helpers';
import {
    CardHolder,
    CreditCard,
    CVV,
    ExpiryDate,
    InstallmentSelect,
    Installment
} from '../../inputsPagarMe';

export type IAmount = {
    value: number;
    installments: Array<Installment>;
};

export type SubmitValues = {
    cardNumberPasted: boolean;
    brand: string;
} & Fields &
    SecurityFields;

type CreditCardFormProps = {
    onSubmit: (values: SubmitValues) => void;
    handleChange?: (values: FormFields) => void;
    amount?: IAmount;
    noSelectIstallmentLabel: string | undefined;
    publicKey?: string;
    fieldsError?: (erros?: FieldError) => void;
};

export const CreditCardFormPagarMe: React.FC<CreditCardFormProps> = ({
    onSubmit,
    amount,
    noSelectIstallmentLabel,
    publicKey,
    fieldsError
}) => {
    const {
        fields,
        securityFields,
        setSecurityFields,
        brand,
        cardNumberPasted,
        errors,
        setFields,
        setBrand,
        setCardNumberPasted,
        setErrors
    } = useCreditCardForm();

    useEffect(() => {
        const installments = amount?.installments;

        if (!installments?.length) {
            const newFields = {
                ...fields,
                installment: {
                    ...fields['installment'],
                    value: 'PARCELAS NÃO INFORMADAS'
                }
            };
            setFields(newFields);
        }
    }, []);

    const handleSubmit = useCallback(
        async (event: FormEvent) => {
            event.preventDefault();
            const {
                installment: { value: installment }
            } = fields;
            const {
                cardNumber: { value: cardNumber },
                expiryDate: { value: expiryDate },
                securityCode: { value: securityCode },
                nameOnCard: { value: nameOnCard }
            } = securityFields;

            const formErrors = await formFieldsPagarMeValidationHelper.validate(
                {
                    cardNumber,
                    expiryDate,
                    securityCode,
                    nameOnCard,
                    installment
                }
            );

            if (!amount?.installments) {
                delete formErrors.installment;
            }

            if (fieldsError && formErrors) fieldsError(formErrors);

            if (
                FormErrorHelper.isEmpty(formErrors) &&
                FormErrorHelper.isEmpty(errors)
            ) {
                const dados = await FetchPagarMeTokenRequestServiceFactory.builder(
                    publicKey,
                    {
                        card: {
                            number: cardNumber.pagarMeValue,
                            holder_name: nameOnCard.pagarMeValue,
                            exp_month: expiryDate?.pagarMeValue?.expiryMonth,
                            exp_year: expiryDate?.pagarMeValue?.expiryYear,
                            cvv: securityCode.pagarMeValue,
                            label: brand
                        }
                    }
                ).fetch();

                const tokenPagarme = dados.data.id;

                onSubmit({
                    cardNumber,
                    expiryDate,
                    securityCode,
                    nameOnCard,
                    brand,
                    installment,
                    cardNumberPasted,
                    tokenPagarme
                });
            } else {
                setErrors(formErrors);
            }
        },
        [fields, securityFields, cardNumberPasted, errors, onSubmit]
    );

    const handleChange = useCallback(
        (name: FieldsNames, value: string | undefined) => {
            setFields({
                [name as FieldsNames]: {
                    ...fields[name],
                    value
                }
            });
        },
        [fields, setFields]
    );

    const handleSecurityInputChange = useCallback(
        (name: SecurityFieldsNames, data: PostMessageEventDataPagarMe) => {
            const { pagarMeValue, isValid, maskedValue } = data;

            setSecurityFields({
                [name as SecurityFieldsNames]: {
                    ...securityFields[name],
                    value: {
                        pagarMeValue,
                        maskedValue,
                        isValid
                    },
                    valid: isValid
                }
            });
        },
        [securityFields, setSecurityFields]
    );

    const handleOnCreditCardIdentified = useCallback(
        (cardData: Card) => {
            const { id } = cardData;
            setBrand(id);
        },
        [setBrand]
    );

    const handleOnCardValidated = useCallback(() => {
        setErrors({
            cardNumber: []
        });
    }, []);

    const handleOnCardError = useCallback(() => {
        setErrors({
            cardNumber: ['Número do cartão inválido']
        });
    }, []);

    const handleOnCreditCardPaste = () => {
        setCardNumberPasted(true);
    };

    const handleOnCreditCardClear = () => {
        setCardNumberPasted(false);
        setBrand('');
    };

    const handleOnExpiryDateValidated = useCallback(() => {
        setErrors({
            expiryDate: []
        });
    }, []);

    const handleOnExpiryDateError = useCallback(() => {
        setErrors({
            expiryDate: ['Data de expiração inválida']
        });
    }, []);

    const handleOnCvvValidated = useCallback(() => {
        setErrors({
            securityCode: []
        });
    }, []);

    const handleOnCvvError = useCallback(() => {
        setErrors({
            securityCode: ['Código de segurança inválido']
        });
    }, [brand]);

    const handleOnCardHolderValidated = useCallback(() => {
        setErrors({
            nameOnCard: []
        });
    }, []);

    const handleOnCardHolderError = useCallback(() => {
        setErrors({
            nameOnCard: ['Nome no cartão inválido']
        });
    }, []);

    const handleOnInstallmentValidated = useCallback(() => {
        setErrors({
            installment: []
        });
    }, []);

    const handleOnInstallmentSelectError = useCallback(
        (installment: string) => {
            const onInstallmentSelectErrors = formFieldsValidationHelper.validateInstallment(
                installment
            );
            setErrors({
                installment: onInstallmentSelectErrors || []
            });
        },
        []
    );

    return (
        <form
            id='credit-card-form-pagarme'
            className='credit-card-form-pagarme'
            onSubmit={handleSubmit}
        >
            <CreditCard
                name='cardNumber'
                label='Número do cartão'
                className='card-number-pagarme'
                onCardIdentified={handleOnCreditCardIdentified}
                errors={GetFormFieldErrorsByFieldName.get(errors, 'cardNumber')}
                onValueValidated={handleOnCardValidated}
                onValueError={handleOnCardError}
                onPaste={handleOnCreditCardPaste}
                onClear={handleOnCreditCardClear}
                // onBlur={handleOnCreditCardBlur}
                onChange={(data: PostMessageEventDataPagarMe) =>
                    handleSecurityInputChange('cardNumber', data)
                }
            />
            <span id='cardType' />
            <div className={styles.expCvc}>
                <ExpiryDate
                    name='expiryDate'
                    label='Data de expiração'
                    className={`${styles.expiryDate} card-expiry-date-pagarme`}
                    errors={GetFormFieldErrorsByFieldName.get(
                        errors,
                        'expiryDate'
                    )}
                    onValueValidated={handleOnExpiryDateValidated}
                    onValueError={handleOnExpiryDateError}
                    onChange={(data: PostMessageEventDataPagarMe) =>
                        handleSecurityInputChange('expiryDate', data)
                    }
                />
                <CVV
                    name='securityCode'
                    label='CVC / CVV'
                    type='text'
                    placeholder='123'
                    className={`${styles.securityCode} security-code-pagarme`}
                    brand={brand}
                    errors={GetFormFieldErrorsByFieldName.get(
                        errors,
                        'securityCode'
                    )}
                    onValueValidated={handleOnCvvValidated}
                    onValueError={handleOnCvvError}
                    onChange={(data: PostMessageEventDataPagarMe) =>
                        handleSecurityInputChange('securityCode', data)
                    }
                    valid={securityFields.securityCode.valid}
                />
            </div>
            <CardHolder
                name='nameOnCard'
                label='Nome no cartão'
                className='card-holder-pagarme'
                errors={GetFormFieldErrorsByFieldName.get(errors, 'nameOnCard')}
                onValueValidated={handleOnCardHolderValidated}
                onValueError={handleOnCardHolderError}
                onChange={(data: PostMessageEventDataPagarMe) =>
                    handleSecurityInputChange('nameOnCard', data)
                }
            />
            {amount && amount.installments && (
                <InstallmentSelect
                    name='installment'
                    label='Parcelamento'
                    value={fields.installment.value}
                    noSelectIstallmentLabel={noSelectIstallmentLabel}
                    errors={GetFormFieldErrorsByFieldName.get(
                        errors,
                        'installment'
                    )}
                    className='installment'
                    onValueValidated={handleOnInstallmentValidated}
                    onValueError={handleOnInstallmentSelectError}
                    installments={amount?.installments}
                    onChange={(value: string | undefined) =>
                        handleChange('installment', value)
                    }
                />
            )}
        </form>
    );
};
