import { errors } from '../configs/errors';
import { BaseException } from './BaseException';

export class BrowserDataSchemaValidationException extends BaseException {
    constructor(internalMessage: string, error?: any) {
        super(
            internalMessage ||
                errors.FAILED_TO_VALIDATE_BROWSER_DATA_SCHEMA.internalMessage,
            errors.FAILED_TO_VALIDATE_BROWSER_DATA_SCHEMA.message,
            errors.FAILED_TO_VALIDATE_BROWSER_DATA_SCHEMA.code,
            error
        );
    }
}
