import { IEnvironment } from '../../types';
import { api } from '../../configs/api';

export class GetUrlBaseHelper {
    static get(environment?: IEnvironment): string {
        if (environment === 'homolog') {
            return api.pagador.url.homolog;
        }

        if (environment === 'production') {
            return api.pagador.url.prod;
        }

        return api.pagador.url.homolog;
    }
}
