import React, { useState, useCallback, useEffect } from 'react';
import { getCardDataByBrandHelper } from '../../../utils/helpers';
import { Card } from '../../../configs/creditCards';
import { cardFieldsURlMapper } from '../../../configs/card-fields';
import { FieldErrorIcon, NoCardIcon } from '../../icons';
import { SecurityInput, InputProps } from '../security-input';
import { useSecurityFields } from '../../../hooks';

type CreditCardProps = {
    onCardIdentified?: (cardData: Card) => void;
    onValueValidated?: () => void;
    onClear?: () => void;
    onValueError?: () => void;
} & Omit<InputProps, 'id' | 'iframeParam'>;

export const CreditCard: React.FC<CreditCardProps> = ({
    icon,
    name,
    onChange,
    onPaste,
    onClear,
    onBlur,
    onCardIdentified,
    onValueValidated,
    onValueError,
    errors,
    className,
    ...restProps
}) => {
    const [cardIcon, setCardIcon] = useState<React.SVGProps<SVGSVGElement>>(
        icon || <NoCardIcon />
    );
    const [valid, setValid] = useState<boolean>(false);

    const { environment } = useSecurityFields();

    useEffect(() => {
        if (errors?.length) {
            setCardIcon(<FieldErrorIcon />);
        }
    }, [errors]);

    const handleOnInputChange = useCallback(
        (data: PostMessageEventDataPagarMe) => {
            const { brand, isValid, isFilled } = data;
            const cardData = getCardDataByBrandHelper.get(brand as string);

            if (cardData) {
                const { icon } = cardData;

                setCardIcon(icon as React.SVGProps<SVGSVGElement>);
                onCardIdentified && onCardIdentified(cardData);

                setValid(isValid);
                if (isValid) {
                    onValueValidated && onValueValidated();
                }
            } else {
                setCardIcon(<NoCardIcon />);
                setValid(false);
            }

            if (!isFilled) {
                setCardIcon(<NoCardIcon />);
                onClear && onClear();
            }

            onChange && onChange(data);
        },
        [onClear, onChange]
    );

    const handleOnBlur = useCallback(
        (data: PostMessageEventDataPagarMe) => {
            const { isValid, isFilled, brand } = data;

            if (isValid) {
                onValueValidated && onValueValidated();
                const data = {
                    eventName: 'onBlur',
                    field: 'card-cvc-cvv-pagarme',
                    data: {
                        brand
                    }
                };
                const frame = document.getElementById(
                    'input-iframe-card-cvc-cvv-pagarme'
                ) as HTMLIFrameElement;
                if (frame && frame.contentWindow) {
                    frame.contentWindow.postMessage(
                        data,
                        cardFieldsURlMapper.get(environment) as string
                    );
                }
            } else if (isFilled) {
                onValueError && onValueError();
                setCardIcon(<FieldErrorIcon />);
            } else {
                setCardIcon(<NoCardIcon />);
            }

            onBlur && onBlur(data);
        },
        [onBlur, onValueValidated, onValueError, environment]
    );

    const handleOnPaste = useCallback(
        (data: PostMessageEventDataPagarMe) => {
            const { brand, isValid, isFilled } = data;
            const cardData = getCardDataByBrandHelper.get(brand as string);

            if (cardData) {
                const { icon } = cardData;

                setCardIcon(icon as React.SVGProps<SVGSVGElement>);
                onCardIdentified && onCardIdentified(cardData);

                setValid(isValid);
                if (isValid) {
                    onValueValidated && onValueValidated();
                }
                onCardIdentified && onCardIdentified(cardData);
            } else {
                setCardIcon(<NoCardIcon />);
                setValid(false);
            }

            if (!isFilled) {
                setCardIcon(<NoCardIcon />);
                onClear && onClear();
            }

            onPaste && onPaste(data);
            onChange && onChange(data);
        },
        [onClear, onPaste, onChange]
    );

    return (
        <SecurityInput
            id='card-number-pagarme'
            name={name}
            icon={icon || cardIcon}
            errors={errors}
            valid={valid}
            containerClassName={className}
            {...restProps}
            onChange={handleOnInputChange}
            onBlur={handleOnBlur}
            onPaste={handleOnPaste}
        />
    );
};
