import { RequestService } from './RequestService';
import {
    FetchServiceOperatorRequestService,
    FetchServiceOperatorResponseData
} from './FetchServiceOperatorRequestService';

import { GetUrlBaseHelper } from '../../utils/helpers';
import { IEnvironment } from '../../types';

export class FetchServiceOperatorRequestServiceFactory {
    private static fetchServiceOperatorRequestService: FetchServiceOperatorRequestService;

    static builder(
        environment?: IEnvironment,
        encryptKey?: any
    ): FetchServiceOperatorRequestService {
        if (
            !FetchServiceOperatorRequestServiceFactory.fetchServiceOperatorRequestService
        ) {
            const urlBase = GetUrlBaseHelper.get(environment);
            const requestService = new RequestService<FetchServiceOperatorResponseData>(
                `${urlBase}/operadoras/servicos`
            );

            FetchServiceOperatorRequestServiceFactory.fetchServiceOperatorRequestService = new FetchServiceOperatorRequestService(
                requestService,
                { publickey: encryptKey }
            );
        }

        return FetchServiceOperatorRequestServiceFactory.fetchServiceOperatorRequestService;
    }
}
