import { UUIDValidationException } from '../exceptions';
import { FetchUuidData } from '../services/requests/FetchUuidRequestService';
import { UuidRequestDataSchemaValidator } from '../utils/validations/validators';
import { Uuid } from './Uuid';

export class UuidFactory {
    static async builder(data: FetchUuidData): Promise<Uuid> {
        const uuidSchemaValidator = new UuidRequestDataSchemaValidator();
        try {
            await uuidSchemaValidator.validate(data);
        } catch (error) {
            throw new UUIDValidationException(
                `Dados da requisição do UUID inválido(s): ${error.message}`
            );
        }
        const { uuid } = data;
        return new Uuid(uuid);
    }
}
