import { FetchPagarmeTokenException } from '../../exceptions';
import { Model } from './Model';
import { RequestService, RequestResponse } from './RequestService';

export type FetchPagarmeTokenData = {
    id: string;
    type: string;
    created_at: string;
    expires_at: string;
    card: {
        last_four_digits: string;
        holder_name: string;
        exp_month: number;
        exp_year: number;
        brand: string;
        label: string;
    };
};

type FetchPagarmeTokenError = {
    messageerror: string;
    internalmessageerror: string;
};

export type FetchPagarmeTokenSuccessResponseData = {
    id: any;
    statuscode: number;
    card: Array<FetchPagarmeTokenData>;
};

type FetchPagarmeTokenErrorResponseData = {
    statuscode: number;
    error: Array<FetchPagarmeTokenError>;
};

export type FetchPagarmeTokenResponseData = FetchPagarmeTokenData &
    FetchPagarmeTokenErrorResponseData;

export class FetchPagarMeTokenRequestService extends Model<FetchPagarmeTokenData> {
    constructor(
        readonly requestService: RequestService<FetchPagarmeTokenData>,
        readonly body?: any,
        readonly tokenStorageService?: any
    ) {
        super(requestService);
    }

    fetch = async (): Promise<RequestResponse<FetchPagarmeTokenData>> => {
        try {
            const response = await this.post({
                body: JSON.stringify(this.body)
            });

            return response;
        } catch (error) {
            throw new FetchPagarmeTokenException(error);
        }
    };
}
