import { GetUrlBaseHelper } from './../../utils/helpers';
import { RequestService } from './RequestService';
import {
    FetchUuidRequestService,
    FetchUuidResponseData
} from './FetchUuidRequestService';
import { IEnvironment } from '../../types';

export class FetchUuidRequestServiceFactory {
    private static fetchUuidRequestService: FetchUuidRequestService;

    static builder(environment?: IEnvironment): FetchUuidRequestService {
        if (!FetchUuidRequestServiceFactory.fetchUuidRequestService) {
            const urlBase = GetUrlBaseHelper.get(environment);
            const requestService = new RequestService<FetchUuidResponseData>(
                `${urlBase}/pagamentos/uuid`
            );
            FetchUuidRequestServiceFactory.fetchUuidRequestService = new FetchUuidRequestService(
                requestService
            );
        }

        return FetchUuidRequestServiceFactory.fetchUuidRequestService;
    }
}
