import * as yup from 'yup';

export const CreditCardFormCardNumberSchema = yup
    .object()
    .shape({
        adyenEncryptedValue: yup.string().required('Número do cartão inválido'),
        gazinEncryptedValue: yup.string().required('Número do cartão inválido'),
        isValid: yup.boolean().isTrue('Número do cartão inválido')
    })
    .required('Número do cartão inválido');

export const CreditCardFormExpiryDateSchema = yup
    .object()
    .shape({
        adyenEncryptedValue: yup
            .object()
            .shape({
                expiryMonth: yup
                    .string()
                    .required('Data de expiração inválida'),
                expiryYear: yup.string().required('Data de expiração inválida')
            })
            .required('Data de expiração inválida'),
        gazinEncryptedValue: yup.string().required('Data de expiração inválida'),
        isValid: yup.boolean().isTrue('Data de expiração inválida')
    })
    .required('Data de expiração inválida');

export const CreditCardFormSecurityCodeSchema = yup
    .object()
    .shape({
        adyenEncryptedValue: yup
            .string()
            .required('Código de segurança inválido'),
        gazinEncryptedValue: yup
            .string()
            .required('Código de segurança inválido'),
            isValid: yup.boolean().isTrue('Código de segurança inválido')
    })
    .required('Data de expiração inválida');

export const CreditCardFormNameOnCardSchema = yup
    .object()
    .shape({
        adyenEncryptedValue: yup.string().required('Nome no cartão inválido'),
        gazinEncryptedValue: yup.string().required('Nome no cartão inválido'),
        isValid: yup.boolean().isTrue('Nome do cartão inválido')
    })
    .required('Nome no cartão inválido');
