import { RequestService } from './RequestService';
import {
    FetchPagarmeTokenData,
    FetchPagarMeTokenRequestService
} from './FetchPagarMeTokenRequestService';

import { api } from '../../configs/api';

export class FetchPagarMeTokenRequestServiceFactory {
    private static fetchPagarMeTokenRequestService: FetchPagarMeTokenRequestService;

    static builder(token?: any, data?: any): FetchPagarMeTokenRequestService {
        if (
            !FetchPagarMeTokenRequestServiceFactory.fetchPagarMeTokenRequestService
        ) {
            const requestService = new RequestService<FetchPagarmeTokenData>(
                `${api.pagarMe.url}/tokens?appId=${token}`
            );

            FetchPagarMeTokenRequestServiceFactory.fetchPagarMeTokenRequestService = new FetchPagarMeTokenRequestService(
                requestService,
                data
            );
        }

        return FetchPagarMeTokenRequestServiceFactory.fetchPagarMeTokenRequestService;
    }
}
