import * as yup from 'yup';

export const BrowserDataSchema = yup.object().shape({
    hostname: yup.string().required('Atributo hostname é obrigatório'),
    useragent: yup.string().required('Atributo useragent é obrigatório'),
    ipaddress: yup.string().required('Atributo ipaddress é obrigatório'),
    type: yup.string().required('Atributo type é obrigatório'),
    language: yup.string().required('Atributo language é obrigatório'),
    cookiesaccepted: yup
        .boolean()
        .required('Atributo cookiesaccepted é obrigatório'),
    javaenabled: yup.boolean().required('Atributo javaenabled é obrigatório'),
    screenheight: yup.number().required('Atributo screenheight é obrigatório'),
    screenwidth: yup.number().required('Atributo screenwidth é obrigatório'),
    colordepth: yup.number().required('Atributo colordepth é obrigatório'),
    timezoneoffset: yup
        .number()
        .required('Atributo timezoneoffset é obrigatório')
});
