import { FetchFingerPrintException } from '../../exceptions';
import { Model } from './Model';
import { RequestService, RequestResponse } from './RequestService';

export type FetchFingerPrintData = number;

type FetchFingerPrintError = {
    messageerror: string;
    internalmessageerror: string;
};

type FetchFingerPrintSuccessResponseData = {
    statuscode: number;
    data: FetchFingerPrintData;
};

type FetchFingerPrintErrorResponseData = {
    statuscode: number;
    error: Array<FetchFingerPrintError>;
};

export type FetchFingerPrintResponseData = FetchFingerPrintSuccessResponseData &
    FetchFingerPrintErrorResponseData;

export class FetchFingerPrintRequestService extends Model<FetchFingerPrintResponseData> {
    constructor(
        readonly requestService: RequestService<FetchFingerPrintResponseData>,
        readonly tokenStorageService?: any
    ) {
        super(requestService);
    }

    fetch = async (): Promise<
        RequestResponse<FetchFingerPrintResponseData>
    > => {
        try {
            const response = await this.get();
            return response;
        } catch (error) {
            throw new FetchFingerPrintException(error);
        }
    };
}
