import * as yup from 'yup';

export const FingerPrintSchema = yup.object().shape({
    fingerprintid: yup
        .number()
        .required('Atributo fingerprintid é obrigatório'),
    uuid: yup.string().required('Atributo uuid é obrigatório'),
    devicefingerprint: yup
        .string()
        .required('Atributo devicefingerprint é obrigatório')
});
