import { FormFieldsValidationHelper } from './FormFieldsValidationHelper';
import {
    CreditCardFormCardNumberSchemaValidator,
    CreditCardFormExpiryDateSchemaValidator,
    CreditCardFormNameOnCardSchemaValidator,
    CreditCardFormSecurityCodeSchemaValidator
} from '../validations/validators';
import { CardInstallmentValidationHelper } from './CardInstallmentValidationHelper';

export class FormFieldsValidationHelperFactory {
    private static formFieldsValidationHelper: FormFieldsValidationHelper;

    private constructor() {}

    static builder(): FormFieldsValidationHelper {
        if (!FormFieldsValidationHelperFactory.formFieldsValidationHelper) {
            const cardInstallmentValidationHelper = new CardInstallmentValidationHelper();
            const creditCardFormCardNumberSchemaValidator = new CreditCardFormCardNumberSchemaValidator();
            const creditCardFormExpiryDateSchemaValidator = new CreditCardFormExpiryDateSchemaValidator();
            const creditCardFormNameOnCardSchemaValidator = new CreditCardFormNameOnCardSchemaValidator();
            const creditCardFormSecurityCodeSchemaValidator = new CreditCardFormSecurityCodeSchemaValidator();

            FormFieldsValidationHelperFactory.formFieldsValidationHelper = new FormFieldsValidationHelper(
                cardInstallmentValidationHelper,
                creditCardFormCardNumberSchemaValidator,
                creditCardFormExpiryDateSchemaValidator,
                creditCardFormNameOnCardSchemaValidator,
                creditCardFormSecurityCodeSchemaValidator
            );
        }

        return FormFieldsValidationHelperFactory.formFieldsValidationHelper;
    }
}

export const formFieldsValidationHelper = FormFieldsValidationHelperFactory.builder();
