import {
    FieldError,
    SecurityFieldValue,
    ExpiryDateSecurityFieldValue
} from '../../types/creditCardForm';
import { CardInstallmentValidationHelper } from './CardInstallmentValidationHelper';
import {
    CreditCardFormPagarMeCardNumberSchemaValidator,
    CreditCardFormPagarMeExpiryDateSchemaValidator,
    CreditCardFormPagarMeNameOnCardSchemaValidator,
    CreditCardFormPagarMeSecurityCodeSchemaValidator
} from '../validations/validators';

type ValidateParams = {
    installment: string;
    cardNumber: SecurityFieldValue;
    expiryDate: ExpiryDateSecurityFieldValue;
    securityCode: SecurityFieldValue;
    nameOnCard: SecurityFieldValue;
};

export class FormFieldsPagarMeValidationHelper {
    constructor(
        readonly cardInstallmentValidationHelper: CardInstallmentValidationHelper,
        readonly creditCardFormPagarMeCardNumberSchemaValidator: CreditCardFormPagarMeCardNumberSchemaValidator,
        readonly creditCardFormPagarMeExpiryDateSchemaValidator: CreditCardFormPagarMeExpiryDateSchemaValidator,
        readonly creditCardFormPagarMeNameOnCardSchemaValidator: CreditCardFormPagarMeNameOnCardSchemaValidator,
        readonly creditCardFormPagarMeSecurityCodeSchemaValidator: CreditCardFormPagarMeSecurityCodeSchemaValidator
    ) {}

    async validate(formFields: ValidateParams): Promise<FieldError> {
        const {
            cardNumber,
            expiryDate,
            securityCode,
            nameOnCard,
            installment
        } = formFields;

        const [
            cardNumberSchemaErrors,
            expiryDateSchemaErrors,
            securityCodeSchemaErrors,
            nameOnCardSchemaErrors
        ] = await Promise.all([
            this.validateCardNumber(cardNumber),
            this.validateExpiryDate(expiryDate),
            this.validateSecurityCode(securityCode),
            this.validateNameOnCard(nameOnCard)
        ]);

        const fieldErrors = {
            cardNumber: cardNumberSchemaErrors,
            expiryDate: expiryDateSchemaErrors,
            securityCode: securityCodeSchemaErrors,
            nameOnCard: nameOnCardSchemaErrors,
            installment: this.validateInstallment(installment)
        };

        let errors = {};
        for (const [key, value] of Object.entries(fieldErrors)) {
            if (value.length !== 0) {
                errors = {
                    ...errors,
                    [key]: value
                };
            }
        }

        return errors;
    }

    async validateCardNumber(
        cardNumber: SecurityFieldValue
    ): Promise<string[]> {
        let cardNumberError: string[] = [];
        try {
            await this.creditCardFormPagarMeCardNumberSchemaValidator.validate(
                cardNumber
            );
        } catch (error) {
            cardNumberError = [error.message];
        }

        return cardNumberError;
    }

    async validateExpiryDate(
        expiryDate: ExpiryDateSecurityFieldValue
    ): Promise<string[]> {
        let expiryDateError: string[] = [];
        try {
            await this.creditCardFormPagarMeExpiryDateSchemaValidator.validate(
                expiryDate
            );
        } catch (error) {
            expiryDateError = [error.message];
        }

        return expiryDateError;
    }

    async validateSecurityCode(
        securityCode: SecurityFieldValue
    ): Promise<string[]> {
        let securityCodeError: string[] = [];
        try {
            await this.creditCardFormPagarMeSecurityCodeSchemaValidator.validate(
                securityCode
            );
        } catch (error) {
            securityCodeError = [error.message];
        }

        return securityCodeError;
    }

    async validateNameOnCard(
        nameOnCard: SecurityFieldValue
    ): Promise<string[]> {
        let nameOnCardError: string[] = [];
        try {
            await this.creditCardFormPagarMeNameOnCardSchemaValidator.validate(
                nameOnCard
            );
        } catch (error) {
            nameOnCardError = [error.message];
        }

        return nameOnCardError;
    }

    validateInstallment(nameOnCard: string): string[] {
        if (!this.cardInstallmentValidationHelper.isValid(nameOnCard)) {
            return ['Opção de parcelamento inválido'];
        }
        return [];
    }
}
