import { creditCards, Card } from '../../configs/creditCards';

export class GetCardDataByBrandHelper {
    get(brand: string): Card | null {
        if (!brand) {
            return null;
        }

        const brandWithoutSpaces = String(brand).replace(/\s/g, '');

        if (brandWithoutSpaces.length === 0) {
            return null;
        }

        return creditCards[brandWithoutSpaces] || null;
    }
}
