import { IDateFormatter, DateFormatter } from './DateFormatter';

export class DateFormatterFactory {
    private static dateFormatter: IDateFormatter;

    static builder(): IDateFormatter {
        if (DateFormatterFactory.dateFormatter) {
            return DateFormatterFactory.dateFormatter;
        }
        DateFormatterFactory.dateFormatter = new DateFormatter();
        return DateFormatterFactory.dateFormatter;
    }
}

export const dateFormatter = DateFormatterFactory.builder();
