import {
    CreditCardFormPagarMeCardNumberSchemaValidator,
    CreditCardFormPagarMeExpiryDateSchemaValidator,
    CreditCardFormPagarMeNameOnCardSchemaValidator,
    CreditCardFormPagarMeSecurityCodeSchemaValidator
} from '../validations/validators';
import { CardInstallmentValidationHelper } from './CardInstallmentValidationHelper';
import { FormFieldsPagarMeValidationHelper } from './FormFieldsValidationHelperPagarMe';

export class FormFieldsPagarMeValidationHelperFactory {
    private static formFieldsPagarMeValidationHelper: FormFieldsPagarMeValidationHelper;

    private constructor() {}

    static builder(): FormFieldsPagarMeValidationHelper {
        if (
            !FormFieldsPagarMeValidationHelperFactory.formFieldsPagarMeValidationHelper
        ) {
            const cardInstallmentValidationHelper = new CardInstallmentValidationHelper();
            const creditCardFormPagarMeCardNumberSchemaValidator = new CreditCardFormPagarMeCardNumberSchemaValidator();
            const creditCardFormPagarMeExpiryDateSchemaValidator = new CreditCardFormPagarMeExpiryDateSchemaValidator();
            const creditCardFormPagarMeNameOnCardSchemaValidator = new CreditCardFormPagarMeNameOnCardSchemaValidator();
            const creditCardFormPagarMeSecurityCodeSchemaValidator = new CreditCardFormPagarMeSecurityCodeSchemaValidator();

            FormFieldsPagarMeValidationHelperFactory.formFieldsPagarMeValidationHelper = new FormFieldsPagarMeValidationHelper(
                cardInstallmentValidationHelper,
                creditCardFormPagarMeCardNumberSchemaValidator,
                creditCardFormPagarMeExpiryDateSchemaValidator,
                creditCardFormPagarMeNameOnCardSchemaValidator,
                creditCardFormPagarMeSecurityCodeSchemaValidator
            );
        }

        return FormFieldsPagarMeValidationHelperFactory.formFieldsPagarMeValidationHelper;
    }
}

export const formFieldsPagarMeValidationHelper = FormFieldsPagarMeValidationHelperFactory.builder();
