import React, { useCallback, useEffect, useState } from 'react';
import { IAmount, SubmitValues } from './components/CreditCard/CreditCardForm';
import { CreditCardShimmer } from './components/Shimmer';
import { IConfigData, useConfig, useSecurityFields } from './hooks';
import { IEnvironment, IPaymentData } from './types';
import { BaseErrorFactory, IBaseError } from './entities';
import { usePayment } from './hooks/usePayment';
import { dfDo } from './scripts/adyentFingerPrint';
import cleanSale from './scripts/cleanSaleScript';

import './styles.module.css';

import { CreditCard } from './components/CreditCard';
import { CreditCardPagarMe } from './components/CreditCardPagarMe';
import { FetchServiceOperatorRequestServiceFactory } from './services/requests';

export type FieldsError = {
    cardNumber: string[];
    expiryDate: string[];
    securityCode: string[];
    nameOnCard: string[];
    installment: string[];
};

export type PaymentProps = {
    amount?: IAmount;
    environment: IEnvironment;
    encryptKey: string;
    merchantaccount: number;
    split: boolean;
    config?: IConfigData;
    onSubmit: (data: IPaymentData) => void;
    onError?: (error: IBaseError, exception?: any) => void;
    fieldsError?: (erros: FieldsError) => void;
};

type Data = {
    merchantaccount?: string;
    servicoSplit?: number;
    publickey?: string;
    tipocredencial?: number;
    error?: boolean;
};

export const Payment: React.FC<PaymentProps> = ({
    amount,
    environment,
    encryptKey,
    merchantaccount,
    split,
    config,
    onSubmit,
    onError,
    fieldsError
}) => {
    const [adyenFingerPrint, setAdyenFingerPrint] = useState<
        string | undefined
    >(undefined);

    const [dadosComponente, setDadosComponente] = useState<Data>();
    const [checkVisibleButtonPayment, setCheckVisibleButtonPayment] = useState< boolean | undefined>(false);

    const { setConfig } = useConfig();
    const {
        formatPaymentData,
        formatPaymentDataPagarme,
        isLoaldingComponent,
        browserData,
        isLoading,
        uuid,
        fingerPrint,
        error
    } = usePayment({ environment });

    const { setInitState } = useSecurityFields();

    useEffect(() => {
        try {
            dfDo('adyenfingerprint');
            // @ts-ignore
            setAdyenFingerPrint(
                (document.getElementById(
                    'adyenfingerprint'
                ) as HTMLInputElement)?.value
            );
        } catch (error) {
            const err = BaseErrorFactory.build(
                'Erro ao inicializar pagamento',
                error
            );
            onError && onError(err);
        }
        setCheckVisibleButtonPayment(config?.checkoutButtonVisible)
        setConfig(config);
    }, []);

    useEffect(() => {
        setInitState({ environment, encryptKey });
    }, [environment, encryptKey, setInitState]);

    useEffect(() => {
        if (error) {
            onError && onError(error);
        }
    }, [error]);

    useEffect(() => {
        if (uuid) {
            cleanSale(
                window,
                document,
                'script',
                '//device.clearsale.com.br/p/fp.js',
                'csdp'
            );
            // @ts-ignore
            csdp('app', '2rcnhjukuwtjlbzxv7w9');
            // @ts-ignore
            csdp('inputsessionid', 'clean-sale-session-id');
        }
    }, [uuid]);

    async function verifyPagador(
        environment: IEnvironment,
        encryptKey: any,
        split: boolean | undefined
    ) {
        try {
            const result = await FetchServiceOperatorRequestServiceFactory.builder(
                environment,
                encryptKey
            ).fetch();

            result.data.data.servicos.forEach((element: any) => {
                if (split) {
                    if (element.idservico === 2 && element.idoperadora === 1) {
                        const merchant = element.merchantaccount.find(
                            (mercha: any) =>
                                mercha.merchantaccount === merchantaccount &&
                                mercha.tipocredencial === 1
                        );

                        if (!merchant) {
                            isLoaldingComponent();
                            onError &&
                                onError({
                                    name: 'Merchantaccount ',
                                    message: 'Merchantaccount',
                                    clientMessage:
                                        'Forma de Pagamento não cadastrada',
                                    code: '400'
                                });
                        }

                        if (merchant?.tipocredencial === 1) {
                            setDadosComponente({
                                servicoSplit: 2,
                                ...merchant
                            });
                        }
                    }
                }

                if (!split) {
                    if (element.idservico === 1) {
                        if (element.idoperadora === 2) {
                            const merchant = element.merchantaccount.find(
                                (mercha: any) =>
                                    mercha.merchantaccount ===
                                        merchantaccount &&
                                    mercha.tipocredencial === 3
                            );

                            if (!merchant) {
                                isLoaldingComponent();
                                onError &&
                                    onError({
                                        name: 'Merchantaccount ',
                                        message: 'Merchantaccount',
                                        clientMessage:
                                            'Forma de Pagamento não cadastrada',
                                        code: '400'
                                    });
                            }

                            if (merchant?.tipocredencial === 1) {
                                setDadosComponente({
                                    servicoSplit: 2,
                                    ...merchant
                                });
                            }
                        }
                        if (element.idoperadora === 3) {
                            const merchant = element.merchantaccount.find(
                                (mercha: any) =>
                                    mercha.merchantaccount ===
                                        merchantaccount &&
                                    mercha.tipocredencial === 3
                            );

                            if (!merchant) {
                                isLoaldingComponent();
                                onError &&
                                    onError({
                                        name: 'Merchantaccount ',
                                        message: 'Merchantaccount',
                                        clientMessage:
                                            'Forma de Pagamento não cadastrada',
                                        code: '400'
                                    });
                            }

                            if (merchant?.tipocredencial === 3) {
                                setDadosComponente({
                                    servicoSplit: 3,
                                    ...merchant
                                });
                            }
                        }
                    }
                }
            });
        } catch (error) {
            const err = BaseErrorFactory.build(
                'Erro ao inicializar pagamento',
                error
            );
            onError && onError(err);
        }
    }

    useEffect(() => {
        verifyPagador(environment, encryptKey, split);
    }, []);

    const handlerFieldsError = useCallback(
        async (errors?: FieldsError) => {
            if (fieldsError && errors) fieldsError(errors);
        },
        [fieldsError]
    );

    const handleSendData = useCallback(
        async (values: SubmitValues) => {
            const {
                cardNumberPasted,
                cardNumber,
                expiryDate,
                securityCode,
                nameOnCard,
                installment,
                brand,
                tokenPagarme
            } = values;
            try {
                setCheckVisibleButtonPayment(true);
                if (tokenPagarme) {
                    const paymentPagarme = await formatPaymentDataPagarme({
                        cardtoken: tokenPagarme,
                        cardNumberPasted,
                        cardNumber: cardNumber.pagarMeValue,
                        expiryDate:
                            expiryDate?.pagarMeValue?.expiryMonth +
                            '/' +
                            expiryDate?.pagarMeValue?.expiryYear,
                        securityCode: securityCode.pagarMeValue,
                        nameOnCard: nameOnCard.pagarMeValue,
                        installment,
                        brand,
                        fingerPrint,
                        browserData,
                        uuid,
                        adyenFingerPrint
                    });

                    await onSubmit(paymentPagarme);
                }
                if (!tokenPagarme) {
                    const paymentData = await formatPaymentData({
                        cardNumberPasted,
                        cardNumber,
                        expiryDate,
                        securityCode,
                        nameOnCard,
                        installment,
                        brand,
                        fingerPrint,
                        browserData,
                        uuid,
                        adyenFingerPrint
                    });
                    await onSubmit(paymentData);
                }
            } catch (error) {
                const err = BaseErrorFactory.build(
                    `Erro ao gerar dados de pagamento`,
                    error
                );
                onError && onError(err);
            } finally {
                setCheckVisibleButtonPayment(true)
            }
        },
        [fingerPrint, browserData, uuid, adyenFingerPrint, onSubmit, onError]
    );

    return (
        <div id='payment-container' className='container'>
            <div className='payment-list'>
                <div className='payment-method'>
                    <input
                        type='hidden'
                        name='adyenfingerprint'
                        id='adyenfingerprint'
                    />
                    {uuid?.value && (
                        <input
                            type='hidden'
                            id='clean-sale-session-id'
                            value={uuid.value}
                        />
                    )}
                    {isLoading ? (
                        <CreditCardShimmer />
                    ) : dadosComponente?.servicoSplit === 3 ? (
                        <CreditCardPagarMe
                            amount={amount}
                            publicKey={dadosComponente?.publickey}
                            onSubmit={handleSendData}
                            disabledButton={checkVisibleButtonPayment}
                            fieldsError={handlerFieldsError}
                        />
                    ) : (
                        <CreditCard
                            amount={amount}
                            onSubmit={handleSendData}
                            fieldsError={handlerFieldsError}
                            disabledButton={checkVisibleButtonPayment}
                        />
                    )}
                </div>
            </div>
        </div>
    );
};
