export class MoneyFormatter {
    constructor(
        readonly locales: string | string[],
        readonly options: Intl.NumberFormatOptions
    ) {}

    format(value: number) {
        let newValue = value;
        if (Number.isNaN(value)) {
            newValue = 0;
        }
        return newValue.toLocaleString(this.locales, this.options);
    }
}
