import { errors } from '../configs/errors';
import { BaseException } from './BaseException';

export class FetchBrowserDataException extends BaseException {
    constructor(error?: any) {
        super(
            errors.FAILED_TO_FETCH_BROWSER_DATA.internalMessage,
            errors.FAILED_TO_FETCH_BROWSER_DATA.message,
            errors.FAILED_TO_FETCH_BROWSER_DATA.code,
            error
        );
    }
}
