import { FetchUUIDException } from '../../exceptions';
import { Model } from './Model';
import { RequestService, RequestResponse } from './RequestService';

export type FetchUuidData = {
    uuid: string;
};

type FetchUuidError = {
    messageerror: string;
    internalmessageerror: string;
};

type FetchUuidSuccessResponseData = {
    statuscode: number;
    data: FetchUuidData;
};

type FetchUuidErrorResponseData = {
    statuscode: number;
    error: Array<FetchUuidError>;
};

export type FetchUuidResponseData = FetchUuidSuccessResponseData &
    FetchUuidErrorResponseData;

export class FetchUuidRequestService extends Model<FetchUuidResponseData> {
    constructor(
        readonly requestService: RequestService<FetchUuidResponseData>,
        readonly tokenStorageService?: any
    ) {
        super(requestService);
    }

    fetch = async (): Promise<RequestResponse<FetchUuidResponseData>> => {
        try {
            const response = await this.get();
            return response;
        } catch (error) {
            throw new FetchUUIDException(error);
        }
    };
}
