export type Format = {
    length: number;
    format: string;
    from: RegExp;
    to: string;
    formatOnTyping: (value: string) => string;
};

export type Card = {
    id: string;
    description: string;
    pattern: RegExp;
    IINRangesPattern: RegExp;
    cvvPattern: RegExp;
    icon: React.SVGProps<SVGSVGElement>;
    formats: Array<Format>;
};

type Cards = {
    [key: string]: Card;
};

