import React, { ReactNode } from 'react';
import { CreditCardFormProvider } from './useCreditCardForm';
import { ConfigProvider } from './useConfig';
import { SecurityFieldsProvider } from './useSecurityFields';

type PaymentComponentProviderProps = {
    children: ReactNode;
};

const PaymentComponentProvider = ({
    children
}: PaymentComponentProviderProps) => {
    return (
        <ConfigProvider>
            <SecurityFieldsProvider>
                <CreditCardFormProvider>{children}</CreditCardFormProvider>
            </SecurityFieldsProvider>
        </ConfigProvider>
    );
};

export default PaymentComponentProvider;
