import { ValidationHelper } from './ValidationHelper';

export class CardInstallmentValidationHelper implements ValidationHelper {
    isValid(cardNumber: string): boolean {
        if (!cardNumber) {
            return false;
        }

        const cardNumberWithoutSpaces = String(cardNumber).replace(/\s/g, '');

        if (cardNumberWithoutSpaces.length === 0) {
            return false;
        }

        return true;
    }
}
