import { FingerPrint, Uuid } from '../../entities';
import {
    BrowserDataSchemaValidationException,
    CreditCardSchemaValidationException,
    FingerPrintSchemaValidationException,
    PaymentSchemaValidationException
} from '../../exceptions';
import { IPaymentData, ICreditCardData, IBrowser } from '../../types';
import {
    CreditCardSchemaValidator,
    PaymentSchemaValidator,
    FingerPrintSchemaValidator,
    BrowserDataSchemaValidator
} from '../validations/validators';

type Data = {
    fingerPrint?: FingerPrint;
    adyenFingerPrint?: string;
    browserData?: IBrowser;
    uuid?: Uuid;
    cardNumber?: {
        adyenEncryptedValue: string;
        gazinEncryptedValue: string;
        maskedValue?: string;
    };
    expiryDate?: {
        adyenEncryptedValue: {
            expiryMonth: string;
            expiryYear: string;
        };
        gazinEncryptedValue: string;
    };
    securityCode?: {
        adyenEncryptedValue: string;
        gazinEncryptedValue: string;
    };
    nameOnCard?: {
        adyenEncryptedValue: string;
        gazinEncryptedValue: string;
    };
    installment: string;
    cardNumberPasted: boolean;
    brand: string;
};

export class PaymentDataBuilderHelper {
    constructor(
        private readonly creditCardSchemaValidator: CreditCardSchemaValidator,
        private readonly paymentSchemaValidator: PaymentSchemaValidator,
        private readonly fingerPrintSchemaValidator: FingerPrintSchemaValidator,
        private readonly browserDataSchemaValidator: BrowserDataSchemaValidator
    ) {}

    build = async (data: Data): Promise<IPaymentData> => {
        const {
            fingerPrint,
            browserData,
            uuid,
            adyenFingerPrint,
            cardNumber,
            expiryDate,
            securityCode,
            nameOnCard,
            installment,
            cardNumberPasted,
            brand
        } = data;

        const creditcard: ICreditCardData = {
            holder: nameOnCard?.adyenEncryptedValue,
            brand,
            insertiontypecardnumber: cardNumberPasted ? 'Colado' : 'Digitado',
            savecard: null,
            encryptedcardnumber: cardNumber?.adyenEncryptedValue,
            maskedcardnumbervalue: cardNumber?.maskedValue as string,
            encryptedexpirymonth: expiryDate?.adyenEncryptedValue?.expiryMonth,
            encryptedexpiryyear: expiryDate?.adyenEncryptedValue?.expiryYear,
            encryptedsecuritycode: securityCode?.adyenEncryptedValue,
            encryptedcardnumberpag: cardNumber?.gazinEncryptedValue,
            encryptedexpirydatepag: expiryDate?.gazinEncryptedValue,
            encryptedsecuritycodepag: securityCode?.gazinEncryptedValue
        };

        const payment = {
            installments: installment
        };

        const fingerPrintData = {
            fingerprintid: fingerPrint?.value as number,
            uuid: uuid?.value as string,
            devicefingerprint: adyenFingerPrint as string
        };

        try {
            await this.creditCardSchemaValidator.validate(creditcard);
        } catch (error) {
            throw new CreditCardSchemaValidationException(error.message, error);
        }

        try {
            await this.paymentSchemaValidator.validate(payment);
        } catch (error) {
            throw new PaymentSchemaValidationException(error.message, error);
        }

        try {
            await this.fingerPrintSchemaValidator.validate(fingerPrintData);
        } catch (error) {
            throw new FingerPrintSchemaValidationException(
                error.message,
                error
            );
        }

        try {
            await this.browserDataSchemaValidator.validate(
                browserData as IBrowser
            );
        } catch (error) {
            throw new BrowserDataSchemaValidationException(
                error.message,
                error
            );
        }

        return {
            creditcard,
            payment,
            fingerPrint: fingerPrintData,
            browserData: browserData as IBrowser
        };
    };
}
