import { RequestErrorFactory } from '../../entities';

/**
 * Request Response
 * @template T Response data
 */
export type RequestResponse<T> = {
    json<P = T>(): Promise<P>;
    data: T;
} & Response;

/**
 * Request promise
 * @template T Response data
 */
export type RequestPromise<T> = Promise<RequestResponse<T>>;

/**
 * Request error
 * @template T Response data
 */
// export type RequestError<T> = AxiosError<T>;

/**
 * Request config such as headers, url, data, paramns, ...
 */
export type RequestConfig = {
    url?: string;
} & RequestInit;

/**
 * Request sergice interface
 * @template T Response data
 * @template C Request config
 */
export interface RequestServiceInterface<T, C> {
    readonly baseUrl: string;
    request: (config: C) => RequestPromise<T>;
}

/**
 * Class representing the Request Service
 * @implements {RequestServiceInterface}
 * @template T Response data
 */
export class RequestService<T>
    implements RequestServiceInterface<T, RequestConfig> {
    private headers: Headers;
    /**
     * Create the request service
     * @param {string} baseUrl Base request url
     */
    constructor(readonly baseUrl: string) {
        this.headers = new Headers({
            Accept: 'application/json',
            'Content-type': 'application/json; charset=UTF-8'
        });
    }

    /**
     * Method that make the request
     * @param {RequestConfig} config Request config such as headers, url, data, paramns, ...
     * @returns {RequestPromise<T>} Returns a request promise with response data T
     */
    request(config: RequestConfig): RequestPromise<T> {
        const { url: newUrl, headers, ...restConfig } = config;
        const url = newUrl || this.baseUrl;
        return fetch(url, {
            headers: {
                ...this.headers,
                ...headers
            },
            ...restConfig
        }).then(async (response) => {
            let data = {} as T;
            try {
                data = await response.json();
            } catch (error) {
                throw new Error('Api sem resposta');
            }

            if (!response.ok) {
                const error = RequestErrorFactory.builder(data as any);
                throw {
                    ...error,
                    url: response.url
                };
            }

            return {
                ...response,
                data
            };
        });
    }
}
