import React, {
    LabelHTMLAttributes,
    SelectHTMLAttributes,
    useCallback,
    ChangeEvent,
    HTMLAttributes
} from 'react';
import styles from './styles.module.css';

export type SelectOption = {
    value: string | number;
    label: string;
};

export type SelectProps = {
    name: string;
    icon?: React.SVGProps<SVGSVGElement>;
    iconProps?: HTMLAttributes<HTMLSpanElement>;
    label?: string;
    labelProps?: LabelHTMLAttributes<HTMLLabelElement>;
    dataCse?: string;
    containerClassName?: string;
    errors?: Array<string>;
    valid?: boolean;
    options: Array<SelectOption>;
    noSelectLabel?: string;
} & SelectHTMLAttributes<HTMLSelectElement>;

export const Select: React.FC<SelectProps> = ({
    id,
    containerClassName,
    icon,
    iconProps: { className: iconClassName, ...restIconProps } = {},
    label,
    labelProps: { className: labelClassName, ...restLabelProps } = {},
    dataCse,
    onChange,
    className,
    errors,
    valid,
    options = [],
    noSelectLabel = 'Selecione',
    ...restProps
}) => {
    const onInputChange = useCallback(
        (event: ChangeEvent<HTMLSelectElement>) => {
            onChange && onChange(event);
        },
        [onChange]
    );

    return (
        <div
            className={`${styles.fieldContainer} ${containerClassName || ''} ${
                errors?.length ? styles.inputError : ''
            } ${valid ? styles.inputValid : ''}`}
        >
            {label && (
                <label
                    htmlFor={id}
                    className={
                        labelClassName
                            ? `${styles.inputLabel} ${labelClassName}`
                            : styles.inputLabel
                    }
                    {...restLabelProps}
                >
                    {label}
                </label>
            )}
            <div className={styles.inputContainer}>
                <select
                    {...restProps}
                    id={id}
                    data-cse={dataCse}
                    onChange={onInputChange}
                    className={
                        className
                            ? `${styles.input} ${className}`
                            : styles.input
                    }
                >
                    <option key='none' value=''>
                        {noSelectLabel}
                    </option>
                    {options.map((option: SelectOption) => (
                        <option key={option.value} value={option.value}>
                            {option.label}
                        </option>
                    ))}
                </select>
                {icon && (
                    <span
                        className={
                            iconClassName
                                ? `${styles.inputIcon} ${iconClassName}`
                                : styles.inputIcon
                        }
                        {...restIconProps}
                    >
                        {icon}
                    </span>
                )}
            </div>
            {errors?.map((error: string, index: number) => (
                <span key={index} className={styles.errorText}>
                    {error}
                </span>
            ))}
        </div>
    );
};
