import {
    PaymentSchemaValidator,
    FingerPrintSchemaValidator,
    BrowserDataSchemaValidator,
    CreditCardPagarMeSchemaValidatorPagarMe
} from '../validations/validators';

import { PaymentPagarMeDataBuilderHelper } from './PaymentPagarMeDataBuilderHelper';

export class PaymentPagarMeDataBuilderHelperFactory {
    private static paymentPagarMeDataBuilderHelper: PaymentPagarMeDataBuilderHelper;

    static builder(): PaymentPagarMeDataBuilderHelper {
        if (
            !PaymentPagarMeDataBuilderHelperFactory.paymentPagarMeDataBuilderHelper
        ) {
            const creditCardPagarMeSchemaValidatorPagarMe = new CreditCardPagarMeSchemaValidatorPagarMe();
            const paymentSchemaValidator = new PaymentSchemaValidator();
            const fingerPrintSchemaValidator = new FingerPrintSchemaValidator();
            const browserDataSchemaValidator = new BrowserDataSchemaValidator();

            PaymentPagarMeDataBuilderHelperFactory.paymentPagarMeDataBuilderHelper = new PaymentPagarMeDataBuilderHelper(
                creditCardPagarMeSchemaValidatorPagarMe,
                paymentSchemaValidator,
                fingerPrintSchemaValidator,
                browserDataSchemaValidator
            );
        }

        return PaymentPagarMeDataBuilderHelperFactory.paymentPagarMeDataBuilderHelper;
    }
}
