import {
    CreditCardSchemaValidator,
    PaymentSchemaValidator,
    FingerPrintSchemaValidator,
    BrowserDataSchemaValidator
} from '../validations/validators';
import { PaymentDataBuilderHelper } from './PaymentDataBuilderHelper';

export class PaymentDataBuilderHelperFactory {
    private static paymentDataBuilderHelper: PaymentDataBuilderHelper;

    static builder(): PaymentDataBuilderHelper {
        if (!PaymentDataBuilderHelperFactory.paymentDataBuilderHelper) {
            const creditCardSchemaValidator = new CreditCardSchemaValidator();
            const paymentSchemaValidator = new PaymentSchemaValidator();
            const fingerPrintSchemaValidator = new FingerPrintSchemaValidator();
            const browserDataSchemaValidator = new BrowserDataSchemaValidator();

            PaymentDataBuilderHelperFactory.paymentDataBuilderHelper = new PaymentDataBuilderHelper(
                creditCardSchemaValidator,
                paymentSchemaValidator,
                fingerPrintSchemaValidator,
                browserDataSchemaValidator
            );
        }

        return PaymentDataBuilderHelperFactory.paymentDataBuilderHelper;
    }
}
