import React, {
    createContext,
    ReactNode,
    useCallback,
    useContext,
    useState
} from 'react';
import {
    FieldError,
    Fields,
    FormFields,
    FormSecurityFields,
    SecurityFieldValue
} from '../types';

export type CreditCardFormFieldsState = {
    brand: string;
    cardNumberPasted: boolean;
} & Fields;

type CreditCardFormProviderProps = {
    children: ReactNode;
};

const initFormSecurityFields = {
    cardNumber: {
        name: 'cardNumber',
        value: {
            adyenEncryptedValue: '',
            gazinEncryptedValue: ''
        },
        valid: false
    },
    expiryDate: {
        name: 'expiryDate',
        value: {
            adyenEncryptedValue: {
                expiryMonth: '',
                expiryYear: ''
            },
            gazinEncryptedValue: ''
        },
        valid: false
    },
    securityCode: {
        name: 'securityCode',
        value: {
            adyenEncryptedValue: '',
            gazinEncryptedValue: ''
        },
        valid: false
    },
    nameOnCard: {
        name: 'nameOnCard',
        value: {
            adyenEncryptedValue: '',
            gazinEncryptedValue: '',
            maskedValue: ''
        },
        valid: false
    }
};

const initFormFields: FormFields = {
    installment: {
        name: 'installment',
        value: '',
        valid: false
    }
};

type CreditCardFormState = {
    fields: FormFields;
    securityFields: FormSecurityFields;
    brand: string;
    cardNumberPasted: boolean;
    errors: FieldError;
    setFields: (changedFields: any) => void;
    setSecurityFields: (changedFields: any) => void;
    setBrand: (brand: string) => void;
    setCardNumberPasted: (cardNumberPasted: boolean) => void;
    setErrors: (errors: FieldError) => void;
};

const initCreditCardFormState = {
    fields: initFormFields,
    securityFields: initFormSecurityFields,
    brand: '',
    cardNumberPasted: false,
    errors: {}
};

const CreditCardFormContext = createContext<CreditCardFormState>(
    initCreditCardFormState as CreditCardFormState
);

const CreditCardFormProvider = ({ children }: CreditCardFormProviderProps) => {
    const [fields, setFields] = useState<FormFields>(
        initCreditCardFormState.fields
    );
    const [securityFields, setSecurityFields] = useState<FormSecurityFields>(
        initCreditCardFormState.securityFields as FormSecurityFields
    );
    const [brand, setBrand] = useState<string>(initCreditCardFormState.brand);
    const [cardNumberPasted, setCardNumberPasted] = useState<boolean>(
        initCreditCardFormState.cardNumberPasted
    );
    const [errors, setErrors] = useState<FieldError>(
        initCreditCardFormState.errors
    );

    const handleSetFields = useCallback(
        (changedFields: any) => {
            setFields((prevState) => ({
                ...prevState,
                ...changedFields
            }));
        },
        [setFields]
    );

    const handleSetSecurityFields = useCallback(
        (changedFields: SecurityFieldValue) => {
            setSecurityFields((prevState) => ({
                ...prevState,
                ...changedFields
            }));
        },
        [setSecurityFields]
    );

    const handleSetBrand = useCallback(
        (value: string) => {
            setBrand(value);
        },
        [setBrand]
    );

    const handleSetCardNumberPastedValue = useCallback(
        (value: boolean) => {
            setCardNumberPasted(value);
        },
        [setCardNumberPasted]
    );

    const handleSetFormErrors = useCallback(
        (errs: FieldError) => {
            setErrors((prevState) => ({
                ...prevState,
                ...errs
            }));
        },
        [setErrors]
    );

    return (
        <CreditCardFormContext.Provider
            value={{
                fields,
                setFields: handleSetFields,
                securityFields,
                setSecurityFields: handleSetSecurityFields,
                brand,
                setBrand: handleSetBrand,
                cardNumberPasted,
                setCardNumberPasted: handleSetCardNumberPastedValue,
                errors,
                setErrors: handleSetFormErrors
            }}
        >
            {children}
        </CreditCardFormContext.Provider>
    );
};

const useCreditCardForm = (): CreditCardFormState => {
    const context = useContext(CreditCardFormContext);

    if (!context) {
        throw new Error(
            'useCreditCardForm must be used within a CreditCardFormProvider'
        );
    }

    return context;
};

export { CreditCardFormProvider, useCreditCardForm };
