import React, {
    LabelHTMLAttributes,
    InputHTMLAttributes,
    HTMLAttributes,
    useEffect,
    useCallback,
    useState,
    Fragment
    // useMemo
} from 'react';
import { cardFieldsURlMapper } from '../../../configs/card-fields';
import {
    HandleSecurityInputIframeMessagesException,
    LoadSecurityInputIframeException
} from '../../../exceptions';
import { useSecurityFields } from '../../../hooks';
import SentryHelperFactory from '../../../utils/helpers/SentryHelperFactory';
import { InputShimmer } from '../../Shimmer';

import styles from './styles.module.css';

export type InputProps = {
    name: string;
    icon?: React.SVGProps<SVGSVGElement>;
    iconProps?: HTMLAttributes<HTMLSpanElement>;
    label?: string;
    labelProps?: LabelHTMLAttributes<HTMLLabelElement>;
    containerClassName?: string;
    errors?: Array<string>;
    valid?: boolean;
    onChange: (value: PostMessageEventData) => void;
    onBlur?: (value: PostMessageEventData) => void;
    onPaste?: (value: PostMessageEventData) => void;
} & Omit<
    InputHTMLAttributes<HTMLInputElement>,
    'onChange' | 'onBlur' | 'onPaste'
>;

export const SecurityInput: React.FC<InputProps> = ({
    id,
    containerClassName,
    icon,
    iconProps: { className: iconClassName, ...restIconProps } = {},
    label,
    labelProps: { className: labelClassName, ...restLabelProps } = {},
    onChange,
    className,
    errors,
    valid,
    onBlur,
    onPaste
}) => {
    const [iframeLoaded, setIframeLoaded] = useState(false);
    const { encryptKey, environment } = useSecurityFields();
    const [iframeUrl, setIframeUrl] = useState('');

    const handleEvent = useCallback(
        (event: any) => {
            try {
                if (
                    event.origin.startsWith(
                        cardFieldsURlMapper.get(environment)
                    )
                ) {
                    const eventData = event.data as PostMessageEvent;
                    if (eventData?.data?.field == id) {
                        const eventName = eventData?.eventName;
                        const data = eventData?.data;
                        if (eventName === 'onChange') {
                            onChange && onChange(data);
                        } else if (eventName === 'onBlur') {
                            onBlur && onBlur(data);
                        } else if (eventName === 'onPaste') {
                            onPaste && onPaste(data);
                        }
                    }
                }
            } catch (error) {
                SentryHelperFactory.builder(environment).handleError(
                    new HandleSecurityInputIframeMessagesException(
                        error.message,
                        event
                    )
                );
            }
        },
        [onChange, onBlur, onPaste, id, environment]
    );

    useEffect(() => {
        window.addEventListener('message', handleEvent);

        setIframeUrl(
            `${cardFieldsURlMapper.get(
                environment
            )}/components/${id}.html?origin=${
                window.location.href
            }&env=${environment}&enckey=${encryptKey}`
        );

        return () => window.removeEventListener('message', handleEvent);
    }, []);

    const handleOnLoad = () => {
        setIframeLoaded(true);
    };

    const handleOnIframeError = () => {
        SentryHelperFactory.builder(environment).handleError(
            new LoadSecurityInputIframeException(
                `Failed to load security input iframe ${id}`
            )
        );
    };

    return (
        <Fragment>
            <div
                className={`${styles.fieldContainer} ${
                    containerClassName || ''
                } ${errors?.length ? styles.inputError : ''} ${
                    valid ? styles.inputValid : ''
                }`}
                style={{ display: iframeLoaded ? 'block' : 'none' }}
            >
                {label && (
                    <label
                        htmlFor={id}
                        className={
                            labelClassName
                                ? `${styles.inputLabel} ${labelClassName}`
                                : styles.inputLabel
                        }
                        {...restLabelProps}
                    >
                        {label}
                    </label>
                )}
                <div className={styles.inputContainer}>
                    <iframe
                        id={`input-iframe-${id}`}
                        className={
                            className
                                ? `${styles.input} ${className}`
                                : styles.input
                        }
                        src={iframeUrl}
                        onLoad={handleOnLoad}
                        onError={handleOnIframeError}
                        onErrorCapture={handleOnIframeError}
                    />
                    {icon && (
                        <span
                            className={
                                iconClassName
                                    ? `${styles.inputIcon} ${iconClassName}`
                                    : styles.inputIcon
                            }
                            {...restIconProps}
                        >
                            {icon}
                        </span>
                    )}
                </div>
                {errors?.map((error: string, index: number) => (
                    <span key={index} className={styles.errorText}>
                        {error}
                    </span>
                ))}
            </div>
            {!iframeLoaded && (
                <div
                    className={`${styles.fieldContainer} ${
                        containerClassName || ''
                    }`}
                >
                    <InputShimmer />
                </div>
            )}
        </Fragment>
    );
};
