import { BRLMoneyFormatter } from './BRLMoneyFormatter';

export class BRLMoneyFormatterFactory {
    private static bRLMoneyFormatter: BRLMoneyFormatter;

    static builder(): BRLMoneyFormatter {
        if (BRLMoneyFormatterFactory.bRLMoneyFormatter) {
            return BRLMoneyFormatterFactory.bRLMoneyFormatter;
        }

        BRLMoneyFormatterFactory.bRLMoneyFormatter = new BRLMoneyFormatter();
        return BRLMoneyFormatterFactory.bRLMoneyFormatter;
    }
}

export const bRLMoneyFormatter = BRLMoneyFormatterFactory.builder();
