import { errors } from '../configs/errors';
import { BaseException } from './BaseException';

export class FetchUUIDException extends BaseException {
    constructor(error?: any) {
        super(
            errors.FAILED_TO_FETCH_UUID.internalMessage,
            errors.FAILED_TO_FETCH_UUID.message,
            errors.FAILED_TO_FETCH_UUID.code,
            error
        );
    }
}
