import * as yup from 'yup';

export const CreditCardSchema = yup.object().shape({
    holder: yup.string().required('Atributo holder é obrigatório'),
    brand: yup.string().required('Atributo brand é obrigatório'),
    insertiontypecardnumber: yup
        .string()
        .required('Atributo insertiontypecardnumber é obrigatório'),
    encryptedcardnumber: yup
        .string()
        .required('Atributo encryptedcardnumber é obrigatório'),
    maskedcardnumbervalue: yup
        .string()
        .required('Atributo maskedCardNumberValue é obrigatório'),
    encryptedexpirymonth: yup
        .string()
        .required('Atributo encryptedexpirymonth é obrigatório'),
    encryptedexpiryyear: yup
        .string()
        .required('Atributo encryptedexpiryyear é obrigatório'),
    encryptedsecuritycode: yup
        .string()
        .required('Atributo encryptedsecuritycode é obrigatório'),
    encryptedcardnumberpag: yup
        .string()
        .required('Atributo encryptedcardnumberpag é obrigatório'),
    encryptedexpirydatepag: yup
        .string()
        .required('Atributo encryptedexpirydatepag é obrigatório'),
    encryptedsecuritycodepag: yup
        .string()
        .required('Atributo encryptedsecuritycodepag é obrigatório')
});
