import { FingerPrintValidationException } from '../exceptions';
import { FetchFingerPrintData } from '../services/requests/FetchFingerPrintRequestService';
import { FingerPrintRequestDataSchemaValidator } from '../utils/validations/validators';
import { FingerPrint } from './FingerPrint';

export class FingerPrintFactory {
    static async builder(data: FetchFingerPrintData): Promise<FingerPrint> {
        const fingerPrintSchemaValidator = new FingerPrintRequestDataSchemaValidator();
        try {
            await fingerPrintSchemaValidator.validate(data);
        } catch (error) {
            throw new FingerPrintValidationException(
                `Dados da requisição do finger print inválido(s): ${error.message}`
            );
        }
        return new FingerPrint(data);
    }
}
