import { IEnvironment } from '../../types';
import { ISentryHelper } from '../../interfaces/Sentry';
import { ProductionSentryHelper } from './ProductionSentryHelper';
import { HomologSentryHelper } from './HomologSentryHelper';
import { DevelopmentSentryHelper } from './DevelopmentSentryHelper';

export default class SentryHelperFactory {
    private static sentryHelper: ISentryHelper;

    static builder(environment?: IEnvironment) {
        if (!SentryHelperFactory.sentryHelper) {
            if (environment === 'production') {
                SentryHelperFactory.sentryHelper = new ProductionSentryHelper();
            } else if (environment === 'homolog') {
                SentryHelperFactory.sentryHelper = new HomologSentryHelper();
            } else {
                SentryHelperFactory.sentryHelper = new DevelopmentSentryHelper();
            }
        }
        return SentryHelperFactory.sentryHelper;
    }

    static getSentryHelper = (): ISentryHelper => {
        return SentryHelperFactory.sentryHelper;
    };
}
