import { RequestError, IRequestError, RequestDataError } from './RequestError';

export class RequestErrorFactory {
    static builder(
        responseError: IRequestError | IRequestError[] | RequestDataError
    ): RequestError {
        if (!responseError) {
            return new RequestError('Erro interno', 'Erro interno');
        }

        const error = (responseError as RequestDataError)?.error;
        if (error) {
            return RequestErrorFactory.buildMessage(error);
        }

        return RequestErrorFactory.buildMessage(
            responseError as IRequestError | IRequestError[]
        );
    }

    private static buildMessage = (error: IRequestError | IRequestError[]) => {
        if (Array.isArray(error)) {
            const errors = error.map((err: IRequestError) => {
                return `${err.messageerror}: ${err.internalmessageerror}`;
            });

            return new RequestError('Erro interno', errors?.join('; '));
        }

        return new RequestError(
            'Erro interno',
            error?.internalmessageerror || ''
        );
    };
}
