import * as yup from 'yup';

export const CreditCardFormPagarMeCardNumberSchema = yup
    .object()
    .shape({
        pagarMeValue: yup.string().required('Número do cartão inválido'),
        isValid: yup.boolean().isTrue('Número do cartão inválido')
    })
    .required('Número do cartão inválido');

export const CreditCardFormPagarMeExpiryDateSchema = yup
    .object()
    .shape({
        pagarMeValue: yup
            .object()
            .shape({
                expiryMonth: yup
                    .string()
                    .required('Data de expiração inválida'),
                expiryYear: yup.string().required('Data de expiração inválida')
            })
            .required('Data de expiração inválida'),
        isValid: yup.boolean().isTrue('Data de expiração inválida')
    })
    .required('Data de expiração inválida');

export const CreditCardFormPagarMeSecurityCodeSchema = yup
    .object()
    .shape({
        pagarMeValue: yup.string().required('Código de segurança inválido'),
        isValid: yup.boolean().isTrue('Código de segurança inválido')
    })
    .required('Data de expiração inválida');

export const CreditCardFormPagarMeNameOnCardSchema = yup
    .object()
    .shape({
        pagarMeValue: yup.string().required('Nome no cartão inválido'),
        isValid: yup.boolean().isTrue('Nome do cartão inválido')
    })
    .required('Nome no cartão inválido');
