import { errors } from '../configs/errors';
import { BaseException } from './BaseException';

export class FetchFingerPrintException extends BaseException {
    constructor(error?: any) {
        super(
            errors.FAILED_TO_FETCH_FINGERPRINT.internalMessage,
            errors.FAILED_TO_FETCH_FINGERPRINT.message,
            errors.FAILED_TO_FETCH_FINGERPRINT.code,
            error
        );
    }
}
