import React, { useState, useCallback, useEffect } from 'react';
import { CheckMarkIcon, CvvIcon, FieldErrorIcon } from '../../icons';
import { SecurityInput, InputProps } from '../security-input';

type CVVProps = {
    brand: string;
    valid?: boolean;
    onValueValidated?: () => void;
    onValueError?: () => void;
} & Omit<InputProps, 'id' | 'iframeParam'>;

export const CVV: React.FC<CVVProps> = ({
    icon,
    onChange,
    onValueValidated,
    onValueError,
    onBlur,
    errors,
    brand,
    className,
    value: valueFromProps,
    valid: validFromProps,
    ...restProps
}) => {
    const [cvvIcon, setCvvIcon] = useState<
        React.SVGProps<SVGSVGElement> | undefined
    >(icon || <CvvIcon />);
    const [valid, setValid] = useState<boolean>(false);

    useEffect(() => {
        if (errors?.length) {
            setCvvIcon(<FieldErrorIcon />);
        }
    }, [errors]);

    const handleOnInputChange = useCallback(
        (data: PostMessageEventDataPagarMe) => {
            const { isFilled, isValid } = data;

            setValid(isValid);
            if (!isFilled) {
                setCvvIcon(<CvvIcon />);
            }
            if (isValid) {
                onValueValidated && onValueValidated();
                setCvvIcon(<CheckMarkIcon />);
            } else {
                setCvvIcon(<CvvIcon />);
            }
            onChange && onChange(data);
        },
        [onChange]
    );

    const handleOnBlur = useCallback(
        (data: PostMessageEventDataPagarMe) => {
            const { isFilled, isValid } = data;

            setValid(isValid);
            if (isValid) {
                onValueValidated && onValueValidated();
                setCvvIcon(<CheckMarkIcon />);
            } else if (isFilled) {
                setCvvIcon(<FieldErrorIcon />);
                onValueError && onValueError();
            } else {
                setCvvIcon(<CvvIcon />);
            }
            onBlur && onBlur(data);
        },
        [onBlur]
    );

    return (
        <div style={{ width: '100%', marginRight: '8px' }}>
            <SecurityInput
                id='card-cvc-cvv-pagarme'
                icon={icon || cvvIcon}
                errors={errors}
                valid={valid}
                containerClassName={className}
                {...restProps}
                value={valueFromProps}
                onChange={handleOnInputChange}
                onBlur={handleOnBlur}
            />
        </div>
    );
};
