import React from 'react';
import { CheckoutButton } from '../CheckoutButton';
import { Header } from './Header';
import { CreditCardFormPagarMe, IAmount } from './CreditCardForm';
import styles from './styles.module.css';
import { useConfig } from '../../hooks';
import { FieldError } from '../../types';

type CreditCardPagarMeProps = {
    amount?: IAmount;
    publicKey?: string;
    onSubmit: (values: any) => void;
    disabledButton:boolean|undefined;
    fieldsError?: (erros?: FieldError) => void;
};

export const CreditCardPagarMe: React.FC<CreditCardPagarMeProps> = ({
    amount,
    publicKey,
    onSubmit,
    disabledButton,
    fieldsError
}) => {
    const {
        paymentTypeTitle,
        checkoutButtonTitle,
        noSelectIstallmentLabel
    } = useConfig();

    const onFormSubmit = (values: any) => {
        const {
            cardNumberPasted,
            cardNumber,
            expiryDate,
            securityCode,
            nameOnCard,
            installment,
            brand,
            tokenPagarme
        } = values;
        onSubmit({
            cardNumberPasted,
            cardNumber,
            expiryDate,
            securityCode,
            nameOnCard,
            installment,
            brand,
            tokenPagarme
        });
    };

    const onFieldsError = (erros?: FieldError) => {
        if (fieldsError && erros) fieldsError(erros);
    };

    return (
        <div className={`${styles.paymentContainer} payment-container`}>
            <div className={`${styles.cardPaymentContainer} card-container`}>
                <Header title={paymentTypeTitle as string} />
                <div className='card-content'>
                    <CreditCardFormPagarMe
                        onSubmit={onFormSubmit}
                        fieldsError={onFieldsError}
                        publicKey={publicKey}
                        amount={amount}
                        noSelectIstallmentLabel={noSelectIstallmentLabel}
                    />
                </div>           
                    <div className={`${styles.cardFooter} card-footer`}>
                        <CheckoutButton
                            form='credit-card-form-pagarme'
                            className='checkout-button'
                            title={checkoutButtonTitle}
                            disabled={disabledButton}
                        />
                    </div>              
            </div>
        </div>
    );
};
