import { BaseException } from '../exceptions/BaseException';
import { IBaseError } from './BaseError';

export class BaseErrorFactory {
    static build(message: string, error: BaseException): IBaseError {
        const {
            clientMessage,
            message: internalMessage,
            code,
            name,
            stack,
            error: objectError
        } = error;
        const newClientMessage = clientMessage
            ? `${message}: ${clientMessage}`
            : `${message}`;

        console.log('BaseErrorFactory', error);
        console.log('BaseErrorFactory', error.message);
        console.log('BaseErrorFactory', error.clientMessage);
        console.log('BaseErrorFactory', error.name);
        console.log('BaseErrorFactory', error.code);
        return {
            name,
            clientMessage: newClientMessage,
            message: internalMessage,
            code,
            stack,
            error: objectError,
            hostname: window.location.hostname,
            exception: error
        };
    }
}
