import React, { useState, useCallback, useEffect } from 'react';
import { CheckMarkIcon, FieldErrorIcon } from '../../icons';
import { SecurityInput, InputProps } from '../security-input';

type ExpiryDateProps = {
    onValueValidated?: () => void;
    onValueError?: () => void;
} & Omit<InputProps, 'id' | 'iframeParam'>;

export const ExpiryDate: React.FC<ExpiryDateProps> = ({
    icon,
    onChange,
    onBlur,
    onValueValidated,
    onValueError,
    maxLength = 5,
    errors,
    className,
    ...restProps
}) => {
    const [expiryDateIcon, setExpiryDateIcon] = useState<
        React.SVGProps<SVGSVGElement> | undefined
    >(icon || undefined);
    const [valid, setValid] = useState<boolean>(false);

    useEffect(() => {
        if (errors?.length) {
            setExpiryDateIcon(<FieldErrorIcon />);
        }
    }, [errors]);

    const handleOnInputChange = useCallback(
        (data: PostMessageEventData) => {
            const { isValid } = data;

            setValid(isValid);
            if (isValid) {
                onValueValidated && onValueValidated();
                setExpiryDateIcon(<CheckMarkIcon />);
            } else {
                setExpiryDateIcon(undefined);
            }

            onChange && onChange(data);
        },
        [onChange]
    );

    const handleOnBlur = useCallback(
        (data: PostMessageEventData) => {
            const { isValid, isFilled } = data;

            if (isValid) {
                setExpiryDateIcon(<CheckMarkIcon />);
                onValueValidated && onValueValidated();
            } else if (isFilled) {
                onValueError && onValueError();
                setExpiryDateIcon(<FieldErrorIcon />);
            } else {
                setExpiryDateIcon(undefined);
            }
            onBlur && onBlur(data);
        },
        [onBlur, onValueValidated, onValueError]
    );

    return (
        <div style={{ width: '100%', marginRight: '8px' }}>
            <SecurityInput
                id='card-expiry-date'
                icon={icon || expiryDateIcon}
                errors={errors}
                valid={valid}
                containerClassName={className}
                {...restProps}
                onChange={handleOnInputChange}
                onBlur={handleOnBlur}
            />
        </div>
    );
};
