import { GetUrlBaseHelper } from './../../utils/helpers';
import { RequestService } from './RequestService';
import {
    FetchFingerPrintRequestService,
    FetchFingerPrintResponseData
} from './FetchFingerPrintRequestService';
import { IEnvironment } from '../../types';

export class FetchFingerPrintRequestServiceFactory {
    private static fetchFingerPrintRequestService: FetchFingerPrintRequestService;

    static builder(environment?: IEnvironment): FetchFingerPrintRequestService {
        if (
            !FetchFingerPrintRequestServiceFactory.fetchFingerPrintRequestService
        ) {
            const urlBase = GetUrlBaseHelper.get(environment);
            const requestService = new RequestService<FetchFingerPrintResponseData>(
                `${urlBase}/pagamentos/digitais`
            );
            FetchFingerPrintRequestServiceFactory.fetchFingerPrintRequestService = new FetchFingerPrintRequestService(
                requestService
            );
        }

        return FetchFingerPrintRequestServiceFactory.fetchFingerPrintRequestService;
    }
}
