import React, {
    createContext,
    ReactNode,
    useCallback,
    useContext,
    useState
} from 'react';

type ConfigProviderProps = {
    children: ReactNode;
};

export type IConfigData = {
    paymentTypeTitle?: string;
    checkoutButtonTitle?: string;
    checkoutButtonVisible?: boolean;
    noSelectIstallmentLabel?: string;
};

export type IConfigState = {
    setConfig: (data: IConfigData | undefined) => void;
} & IConfigData;

const initConfigData: IConfigData = {
    paymentTypeTitle: 'Cartão de crédito',
    checkoutButtonTitle: 'Pagar',
    checkoutButtonVisible: true,
    noSelectIstallmentLabel: 'Selecione uma parcela'
};

const initConfigState: IConfigState = {
    ...initConfigData,
    setConfig: () => {}
};

const ConfigContext = createContext<IConfigState>(initConfigState);

const ConfigProvider = ({ children }: ConfigProviderProps) => {
    const [config, setConfig] = useState<IConfigData>(initConfigData);

    const setConfigOptions = useCallback(
        (data: IConfigData | undefined = {}) => {
            setConfig({
                ...initConfigData,
                ...data
            });
        },
        [setConfig]
    );
    return (
        <ConfigContext.Provider
            value={{ ...config, setConfig: setConfigOptions }}
        >
            {children}
        </ConfigContext.Provider>
    );
};

const useConfig = (): IConfigState => {
    const context = useContext(ConfigContext);

    if (!context) {
        throw new Error('useConfig must be used within a ConfigProvider');
    }

    return context;
};

export { ConfigProvider, useConfig };
