import React, {
    useState,
    useCallback,
    ChangeEvent,
    FocusEvent,
    useMemo,
    useEffect
} from 'react';

import { bRLMoneyFormatter } from '../../../utils/formatters';
import { CheckMarkIcon, FieldErrorIcon } from '../../icons';
import { Select, SelectProps, SelectOption } from '../Select';

export type Installment = {
    value: number; // value
    number: number;
    hasInterest: boolean;
};

type InstallmentSelectProps = {
    onValueValidated?: () => void;
    onValueError?: (cvv: string) => void;
    installments?: Array<Installment>;
    onChange?: (value: string | undefined) => void;
    noSelectIstallmentLabel?: string;
} & Omit<SelectProps, 'options' | 'onChange'>;

export const InstallmentSelect: React.FC<InstallmentSelectProps> = ({
    icon,
    onChange,
    onBlur: onBlurProps,
    onValueValidated,
    onValueError,
    errors,
    className,
    installments = [],
    noSelectIstallmentLabel,
    ...restProps
}) => {
    const [installmentIcon, setInstallmentIcon] = useState<
        React.SVGProps<SVGSVGElement> | undefined
    >(icon || undefined);
    const [value, setValue] = useState<string | undefined>('');
    const [valid, setValid] = useState<boolean>(false);

    useEffect(() => {
        if (errors?.length) {
            setInstallmentIcon(<FieldErrorIcon />);
        }
    }, [errors]);

    const onSelectChange = useCallback(
        (event: ChangeEvent<HTMLSelectElement>) => {
            const valueFromInput = event.target.value;
            setValue(valueFromInput);
            if (valueFromInput) {
                setValid(true);
                setInstallmentIcon(<CheckMarkIcon />);
                onValueValidated && onValueValidated();
            }
            onChange && onChange(valueFromInput);
        },
        [onChange, onValueValidated, value]
    );

    const onBlur = useCallback(
        (event: FocusEvent<HTMLSelectElement>) => {
            const value = event.target.value;

            if (value) {
                onValueValidated && onValueValidated();
                setInstallmentIcon(<CheckMarkIcon />);
            } else {
                onValueError && onValueError(value);
                setInstallmentIcon(<FieldErrorIcon />);
            }
            onBlurProps && onBlurProps(event);
        },
        [onBlurProps, onValueValidated, onValueError]
    );

    const options = useMemo((): Array<SelectOption> => {
        return installments.map(
            (installment: Installment): SelectOption => {
                const { value, hasInterest, number } = installment;
                const valueFormatted = bRLMoneyFormatter.format(value);
                const label = hasInterest
                    ? `x ${valueFormatted} com juros`
                    : `x ${valueFormatted} sem juros`;
                return {
                    value: number,
                    label: `${number}${label}`
                };
            }
        );
    }, [installments]);

    return (
        <Select
            placeholder='Selecione o parcelamento'
            icon={icon || installmentIcon}
            errors={errors}
            valid={valid}
            containerClassName={className}
            noSelectLabel={noSelectIstallmentLabel}
            {...restProps}
            value={value}
            onChange={onSelectChange}
            onBlur={onBlur}
            options={options}
        />
    );
};
