import { MoneyFormatter } from './MoneyFormatter';

export class BRLMoneyFormatter extends MoneyFormatter {
    constructor(readonly intlOptions?: Intl.NumberFormatOptions) {
        super('pt-BR', {
            minimumFractionDigits: 2,
            currency: 'BRL',
            ...intlOptions,
            style: 'currency'
        });
    }
}
