import { FetchBrowserDataException } from '../../exceptions';
import { Model } from './Model';
import { RequestService, RequestResponse } from './RequestService';

export type FetchBrowserDataData = {
    useragent: string;
    browser: string;
    host: string;
    port: string;
    remoteaddr: string;
    scheme: string;
    serveraddr: string;
    servername: string;
};

type FetchBrowserDataError = {
    messageerror: string;
    internalmessageerror: string;
};

type FetchBrowserDataSuccessResponseData = {
    statuscode: number;
    data: FetchBrowserDataData;
};

type FetchBrowserDataErrorResponseData = {
    statuscode: number;
    error: Array<FetchBrowserDataError>;
};

export type FetchBrowserDataResponseData = FetchBrowserDataSuccessResponseData &
    FetchBrowserDataErrorResponseData;

export class FetchBrowserDataRequestService extends Model<FetchBrowserDataResponseData> {
    constructor(
        readonly requestService: RequestService<FetchBrowserDataResponseData>,
        readonly tokenStorageService?: any
    ) {
        super(requestService);
    }

    fetch = async (): Promise<
        RequestResponse<FetchBrowserDataResponseData>
    > => {
        try {
            const response = await this.get();
            return response;
        } catch (error) {
            throw new FetchBrowserDataException(error);
        }
    };
}
